import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatButtonToggleModule } from '@angular/material/button-toggle';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyAutocompleteModule as MatAutocompleteModule } from '@angular/material/legacy-autocomplete';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyChipsModule as MatChipsModule } from '@angular/material/legacy-chips';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatLegacySlideToggleModule as MatSlideToggleModule } from '@angular/material/legacy-slide-toggle';
import { TranslateModule } from '@ngx-translate/core';
import { TeiTextTranslatePipeModule } from "@tei/core-translate";
import { AvatarModule, IconAutocompleteModule, MultipleTreeSelectChipsModule, QuickSearchFilterChipModule } from '@tei/material';
import { WebNavigationService } from '../../../../@services/web-navigation.service';
import { PageStyleSheetDialogModule } from '../page-style-sheet-dialog/page-style-sheet-dialog.module';
import { ManagePageDialogComponent } from './manage-page-dialog.component';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatSelectModule, MatFormFieldModule, MatButtonModule, MatDialogModule, MatSelectModule, MatInputModule,
  MatDividerModule, MatIconModule, MatButtonToggleModule, MatChipsModule, MatRadioModule, MatSlideToggleModule, MatCheckboxModule,
  MatAutocompleteModule
];
const TEI_MATERIAL_MODULES = [AvatarModule, IconAutocompleteModule, QuickSearchFilterChipModule, MultipleTreeSelectChipsModule, PageStyleSheetDialogModule];
const COMPONENTS = [ManagePageDialogComponent];
const TEI_CORE_MODULES = [
  TeiTextTranslatePipeModule,
  TranslateModule
];
@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
  ],
  exports: [
    ...COMPONENTS,
  ],
  providers: [{ provide: 'IWebNavigationService', useClass: WebNavigationService }]
})
export class ManagePageDialogModule { }
