import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatIconModule } from '@angular/material/icon';
import { NumericPadModule } from '@tei/material';
import { DefaultComponent } from './default.component';

const MATERIAL_MODULES = [MatIconModule, MatButtonModule, MatCardModule];
const BASE_MODULES = [CommonModule];
const TEI_MATERIAL_MODULES = [NumericPadModule, FormsModule, ReactiveFormsModule];

@NgModule({
  declarations: [DefaultComponent],
  imports: [
    ...MATERIAL_MODULES,
    ...BASE_MODULES,
    ...TEI_MATERIAL_MODULES
  ],
  providers: [],
})
export class DefaultModule { }
