import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { Page } from '../@intermediate/models/post-web-navigation-responsive-setting.model';

@Injectable()
export class TreeListMenuActionsService
{
  dataChange = new BehaviorSubject<Page[]>([]);

  get data(): Page[] { return this.dataChange.value; }

  initialize(data: Page[])
  {
    this.dataChange.next(data);
  }

  /**
   * Build the file structure tree. The `value` is the Json object, or a sub-tree of a Json object.
   * The return value is the list of `FlatNode`.
   */
  buildFileTree(obj: object, level: number): Page[]
  {
    return Object.keys(obj).reduce<Page[]>((accumulator, key) =>
    {
      const value = obj[key];
      const node = new Page();
      node.PageName = key;

      if (value != null)
      {
        if (typeof value === 'object')
        {
          node.Children = this.buildFileTree(value, level + 1);
        } else
        {
          node.PageName = value;
        }
      }

      return accumulator.concat(node);
    }, []);
  }

  insertItem(parent: Page, name: string, PageUID?: string, Children?: Page[]): Page
  {
    if (!parent)
    {
      const newItem = { UID: this.getGuid(), PageUID: PageUID, PageName: name, SortOrder: 0, Children: [] } as Page;

      this.data.push(newItem);
      this.dataChange.next(this.data);
      return newItem;
    }

    const parentNode = this.getParentFromNodes(parent);
    const newItem = { UID: this.getGuid(), PageUID: PageUID, PageName: name, SortOrder: parent.Children.length + 1, Children: [] } as Page;
    if (parentNode == null)
    {
      let parentToModify = this.data.find(item => item.UID == parent.UID);
      if (!parentToModify.Children)
      {
        parentToModify.Children = [];
      }

      parentToModify.Children.push(newItem);
    } else
    {
      let parentToModify = parentNode.Children.find(item => item.UID == parent.UID);
      if (!parentToModify.Children)
      {
        parentToModify.Children = [];
      }
      parentToModify.Children.push(newItem);
    }

    this.dataChange.next(this.data);
    return newItem;
  }

  insertItemAbove(node: Page, name: string, PageUID?: string, sortOrder?: number): Page
  {
    const parentNode = this.getParentFromNodes(node);
    const newItem = { UID: this.getGuid(), PageUID: PageUID, PageName: name, SortOrder: sortOrder, Children: [], ParentPageUID: node.ParentPageUID } as Page;
    if (parentNode != null)
    {
      parentNode.Children.splice(parentNode.Children.findIndex(item => item.UID == node.UID), 0, newItem);
    } else
    {
      this.data.splice(this.data.findIndex(item => item.UID == node.UID), 0, newItem);
    }
    this.dataChange.next(this.data);
    return newItem;
  }

  insertItemBelow(node: Page, name: string, PageUID?: string): Page
  {
    const parentNode = this.getParentFromNodes(node);
    const newItem = { UID: this.getGuid(), PageUID: PageUID, PageName: name, SortOrder: 0, Children: [], ParentPageUID: node.ParentPageUID } as Page;
    if (parentNode != null)
    {
      parentNode.Children.splice(parentNode.Children.findIndex(item => item.UID == node.UID) + 1, 0, newItem);
    } else
    {
      this.data.splice(this.data.findIndex(item => item.UID == node.UID) + 1, 0, newItem);
    }
    this.dataChange.next(this.data);
    return newItem;
  }

  getParentFromNodes(node: Page): Page
  {
    for (let i = 0; i < this.data.length; ++i)
    {
      const currentRoot = this.data[i];
      const parent = this.getParent(currentRoot, node);
      if (parent != null)
      {
        return parent;
      }
    }
    return null;
  }

  getParent(currentRoot: Page, node: Page): Page
  {
    if (currentRoot.Children && currentRoot.Children.length > 0)
    {
      for (let i = 0; i < currentRoot.Children.length; ++i)
      {
        const child = currentRoot.Children[i];
        if (child.UID === node.UID)
        {
          return currentRoot;
        } else if (child.Children && child.Children.length > 0)
        {
          const parent = this.getParent(child, node);
          if (parent != null)
          {
            return parent;
          }
        }
      }
    }
    return null;
  }

  updateItem(node: Page, name: string)
  {
    node.PageName = name;
    this.dataChange.next(this.data);
  }

  deleteItem(node: Page)
  {
    this.deleteNode(this.data, node);
    this.dataChange.next(this.data);
  }

  reviseSortOrder(): void
  {
    var pages = this.updateSortOrder(this.data);
    this.dataChange.next(this.data);
  }

  private updateSortOrder(pages: Page[]): Page[]
  {
    pages.forEach((page, index) =>
    {
      page.SortOrder = index + 1;
      if (page.Children && page.Children.length > 0)
      {
        this.updateSortOrder(page.Children); // Recurse for children
      }
    });

    return pages;
  }


  reviseSortOrderAndGetSortNumber(page: Page): any
  {
    var sortOrder = this.updateSortOrderAndGetSorNumber(this.data, page);
    this.dataChange.next(this.data);
    return sortOrder;
  }

  private updateSortOrderAndGetSorNumber(pages: Page[], inputPage: Page)
  {
    let sortOrder = 0;
    pages.forEach((page, index) =>
    {
      page.SortOrder = index + 1;
      if (page.UID == inputPage.UID)
        sortOrder = page.SortOrder;
      if (page.Children && page.Children.length > 0)
      {
        let nestSortOrder = this.updateSortOrderAndGetSorNumber(page.Children, inputPage); // Recurse for children
        if (nestSortOrder > 0)
          sortOrder = nestSortOrder;
      }
    });

    return sortOrder;
  }

  copyPasteItem(item: Page, parent: Page): Page
  {
    const newItem = this.insertItem(parent, item.PageName, item.PageUID);
    if (item.Children)
    {
      item.Children.forEach(child =>
      {
        this.copyPasteItem(child, newItem);
      });
    }
    return newItem;
  }

  copyPasteItemAbove(from: Page, to: Page): Page
  {
    const newItem = this.insertItemAbove(to, from.PageName, from.PageUID, from.SortOrder);
    if (from.Children)
    {
      from.Children.forEach(child =>
      {
        this.copyPasteItem(child, newItem);
      });
    }
    return newItem;
  }

  copyPasteItemBelow(from: Page, to: Page): Page
  {
    const newItem = this.insertItemBelow(to, from.PageName, from.PageUID);
    if (from.Children)
    {
      from.Children.forEach(child =>
      {
        this.copyPasteItem(child, newItem);
      });
    }
    return newItem;
  }

  deleteNode(nodes: Page[], nodeToDelete: Page)
  {
    const index = nodes.findIndex(item => item.UID == nodeToDelete.UID);
    if (index > -1)
    {
      nodes.splice(index, 1);
    } else
    {
      nodes.forEach(node =>
      {
        if (node.Children && node.Children.length > 0)
        {
          this.deleteNode(node.Children, nodeToDelete);
        }
      });
    }
  }

  getGuid()
  {
    return "10000000-1000-4000-8000-100000000000".replace(/[018]/g, c =>
      (+c ^ crypto.getRandomValues(new Uint8Array(1))[0] & 15 >> +c / 4).toString(16)
    );
  }
}

/** Flat to-do item node with expandable and level information */
export class FlatNode
{
  PageUID: string;
  ParentPageUID: string;
  Level: number;
  PageName: string;
  SortOrder: number;
  expandable: boolean;
}

