import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { NewBannerComponent } from './new-banner/new-banner.component';
import { BannersComponent } from './banners.component';
import { BannerSortOrderComponent } from './banner-sort-order/banner-sort-order.component';

const routes: Routes = [
  { path: '', component: BannersComponent },
  { path: 'reorder', component: BannerSortOrderComponent },
  { path: 'new/:id', component: NewBannerComponent },
  { path: 'new', component: NewBannerComponent },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class BannersRoutingModule
{
}
