import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialog as MatDialog, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { ImageUploaderComponent, ImageUploaderModel, SnackBarService, TeiTreeChipsModel } from '@tei/material';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import
{
  ApplicableModule,
  ApplicableParentPage,
  ContentPages,
  DevicePageResponsiveSetting,
  DeviceType, GetContentPagesRequest, GetContentPagesResponse,
  GetPageRequest,
  GetPageResponse,
  ManagePageForm,
  PageType
} from '../../../../@intermediate/models/get-page.model';
import { PageIconType, PostPageResponse } from '../../../../@intermediate/models/post-page.model';
import { IWebNavigationService } from '../../../../@intermediate/web-navigation.interface';
import { PageStyleSheetDialogComponent } from '../page-style-sheet-dialog/page-style-sheet-dialog.component';

@Component({
  selector: 'tei-cms-admin-manage-page-dialog',
  templateUrl: './manage-page-dialog.component.html',
  styleUrls: ['./manage-page-dialog.component.scss']
})
export class ManagePageDialogComponent implements OnInit
{
  pageType = PageType;
  DeviceType = DeviceType;
  teiChipData: TeiTreeChipsModel[] = [];
  applicableParentPages: ApplicableParentPage[] = [];
  applicableModules: ApplicableModule[];
  pageData: GetPageResponse;
  devicePageResponsiveSettings: DevicePageResponsiveSetting[];
  selectedResponsiveSetting: DevicePageResponsiveSetting;
  deviceType: DeviceType;
  isAllSelected: boolean;
  allRolesTitle: string;
  cmsInitialized: boolean = false;
  form: FormGroup<ManagePageForm> = new FormGroup<ManagePageForm>({
    PageUID: new FormControl(null),
    PageName: new FormControl('', Validators.required),
    PageTitle: new FormControl('', Validators.required),
    IconFile: new FormControl(null),
    IconUrl: new FormControl(null),
    Icon: new FormControl(null),
    PageType: new FormControl(this.pageType.Module),
    Module: new FormControl([]),
    ContentPageExternalIdentifier: new FormControl(null),
    ExternalURL: new FormControl(null),
    OpenExternalURLInNewTab: new FormControl(null),
    PageVisibilityRoles: new FormControl([], Validators.required),
    VisibleToAnonymous: new FormControl(false),
    ApplicableModules: new FormControl,
    ApplicableParentPage: new FormControl,
    DisplayMenu: new FormControl
  });
  butterCmsPages: ContentPages[] = [];
  filteredModules!: Observable<{ ApplicationHierarchyName: string; }[]>;
  filteredPages!: Observable<ContentPages[]>;
  shouldUpdateStyleSheet: boolean = false;
  styleSheet: any;

  constructor(private translate: TranslateService,
    @Inject('IWebNavigationService') private service: IWebNavigationService,
    public dialogRef: MatDialogRef<ManagePageDialogComponent>,
    private snackBarService: SnackBarService,
    @Inject(MAT_DIALOG_DATA) public data: {
      deviceType: DeviceType,
      title: string,
      pageData: GetPageResponse;
    },
    private dialog: MatDialog,
  ) { }


  ngOnInit(): void
  {
    this.pageData = this.data.pageData;
    this.deviceType = this.data.deviceType;
    this.isAllSelected = this.pageData.PageUID === null;
    if (this.pageData)
    {
      this.form.patchValue({
        PageName: this.pageData.PageName,
        PageTitle: this.pageData.PageTitle,
        IconUrl: this.pageData.IconUrl,
        Icon: this.pageData.Icon !== null ? this.convertIconData(this.pageData.Icon) : null,
        PageType: this.pageData.PageType,
        Module: this.pageData.ApplicableModules?.find(
          (module: ApplicableModule) => module.ApplicationHierarchyUID === this.pageData.Module?.ModuleApplicationHierarchyUID
        ),
        ApplicableModules: this.pageData.ApplicableModules?.map(m => (m.ApplicationHierarchyUID)),
        ContentPageExternalIdentifier: this.pageData.Content?.ContentPageExternalIdentifier,
        ExternalURL: this.pageData.ExternalURL?.ExternalURL,
        OpenExternalURLInNewTab: this.pageData.ExternalURL?.OpenExternalURLInNewTab,
        PageVisibilityRoles: this.pageData.PageVisibilityRoles,
        VisibleToAnonymous: this.pageData.VisibleToAnonymous
      });
      this.applicableModules = this.pageData.ApplicableModules;
      this.applicableParentPages = this.pageData.ApplicableParentPages.sort((a, b) => a.PageName.localeCompare(b.PageName));
      this.applicableParentPages.unshift({ PageUID: null, PageName: "" });
      this.teiChipData = this.pageData.ApplicableRoles?.map(m => ({ id: null, name: m.DesignationName + " Roles", active: true, children: m.Roles.map(q => ({ id: q.RoleUID, name: q.RoleName, active: true })) }));
      this.devicePageResponsiveSettings = this.pageData.DevicePageResponsiveSettings;
      this.deviceTypeChange(this.deviceType);
      this.cmsInitialized = this.pageData.CMSInitialized;
      if (this.cmsInitialized)
      {
        this.loadButterCmsPages();
      }
    }

    this.filteredModules = this.form.get('Module')!.valueChanges.pipe(
      startWith(''),
      map(value => (typeof value === 'string' ? value : '')),
      map(value => this.filterModules(value || ''))
    );
    this.filteredPages = this.form.get("ContentPageExternalIdentifier")!.valueChanges.pipe(
      startWith(''),
      map(value => (typeof value === 'string' ? value : value?.PageName || '')),
      map(value => this.filterPages(value || ''))
    );
    this.form.get('PageType')?.valueChanges.subscribe((value) =>
    {
      this.updateValidators(value);
    });

    this.allRolesTitle = this.translate.instant("MARKETING_CMS_ADMIN.MANAGE_PAGE_DIALOG.ALL_ROLES");
  }

  convertIconData(str: string)
  {
    const [value, iconStyle, fill] = str.split(':');
    if (!value)
    {
      return;
    }
    return {
      value,
      iconStyle,
      fill: fill === 'true'
    };
  }

  private filterModules(value: string): ApplicableModule[]
  {
    const filterValue = value.toLowerCase();
    return this.applicableModules.filter(option =>
      option.ApplicationHierarchyName.toLowerCase().includes(filterValue)
    );
  }

  filterPages(value: string): ContentPages[]
  {
    const filterValue = value.toLowerCase();
    return this.butterCmsPages.filter(page =>
      page.PageName.toLowerCase().includes(filterValue) ||
      page.PageType.toLowerCase().includes(filterValue)
    );
  }

  getModuleName(): string
  {
    const selectedModuleUID = this.form.get('Module')!.value?.ModuleApplicationHierarchyUID;
    const module = this.applicableModules.find(m => m.ApplicationHierarchyUID === selectedModuleUID);
    return module ? module.ApplicationHierarchyName : '';
  }

  getPageName(): string
  {
    const selectedPage = this.form.get('ContentPageExternalIdentifier')!.value;
    const module = this.butterCmsPages.find(m => m.ExternalIdentifier === selectedPage);
    return module ? module.PageName : '';
  }

  displayModule(module: any): string
  {
    return module ? module.ApplicationHierarchyName : '';
  }

  displayPageName(page: ContentPages): string
  {
    return page ? page.PageName : '';
  }

  onModuleSelected(selectedModule: ApplicableModule): void
  {
    this.form.get('Module')!.setValue({
      ModuleApplicationHierarchyUID: selectedModule.ApplicationHierarchyUID
    });
  }

  hasError(controlName: string, errorName: string): boolean
  {
    const control = this.form.get(controlName);
    return control && control.hasError(errorName) && control.touched;
  }

  onRadioButtonChange(event: any): void
  {
    console.log(event);
  }

  deviceTypeChange(deviceType: DeviceType): void
  {
    this.selectedResponsiveSetting = this.devicePageResponsiveSettings.find(
      (device: DevicePageResponsiveSetting) => device.DeviceTypeID === deviceType
    );
    this.deviceType = deviceType;
  }

  changeParentPage(value: string): void
  {
    this.devicePageResponsiveSettings = this.devicePageResponsiveSettings.map(item =>
    {
      if (item.DeviceTypeID === this.deviceType)
      {
        item.ParentPageUID = value;
      }
      return item;
    });
    this.selectedResponsiveSetting.ParentPageUID = value;
  }

  onToggleChange(checked: boolean): void
  {
    this.devicePageResponsiveSettings = this.devicePageResponsiveSettings.map(item =>
    {
      if (item.DeviceTypeID === this.deviceType)
      {
        item.DisplayPageInMenu = checked;
      }
      return item;
    });
    this.selectedResponsiveSetting.DisplayPageInMenu = checked;
  }

  onPageTypeChange(value: PageType): void
  {
  }

  shouldShowPageStyleSheetButton(): boolean
  {
    const currentPageType = this.form.get('PageType')?.value;
    return currentPageType === PageType.Content ||
      currentPageType === PageType.Module;
  }

  updateValidators(pageType: number): void
  {
    this.form.get('Module')?.clearValidators();
    this.form.get('ContentPageExternalIdentifier')?.clearValidators();
    this.form.get('ExternalURL')?.clearValidators();

    switch (pageType)
    {
      case this.pageType.Module:
        this.form.get('Module')?.setValidators(Validators.required);
        break;
      case this.pageType.Content:
        this.form
          .get('ContentPageExternalIdentifier')
          ?.setValidators(Validators.required);
        break;
      case this.pageType.ExternalUrl:
        this.form.get('ExternalURL')?.setValidators(Validators.required);
        break;
    }

    this.form.get('Module')?.updateValueAndValidity();
    this.form
      .get('ContentPageExternalIdentifier')
      ?.updateValueAndValidity();
    this.form.get('ExternalURL')?.updateValueAndValidity();
  }

  isModuleSelected(): boolean
  {
    return this.form.get('PageType')?.value === PageType.Module;
  }

  isContentSelected(): boolean
  {
    return this.form.get('PageType')?.value === PageType.Content;
  }

  isExternalSelected(): boolean
  {
    return this.form.get('PageType')?.value === PageType.ExternalUrl;
  }

  loadButterCmsPages(): void
  {
    const request = new GetContentPagesRequest();
    this.service.GetContentPages(request).subscribe((response: GetContentPagesResponse) =>
    {
      if (response)
      {
        this.butterCmsPages = response.ContentPages;
        if (this.pageData.PageType == this.pageType.Content && this.pageData.Content?.ContentPageExternalIdentifier)
        {
          this.form.get('ContentPageExternalIdentifier').patchValue(this.butterCmsPages.find(item => item.ExternalIdentifier == this.pageData.Content?.ContentPageExternalIdentifier));
        }
        this.filteredPages = this.form.get("ContentPageExternalIdentifier")!.valueChanges.pipe(
          startWith(''),
          map(value => (typeof value === 'string' ? value : value?.PageName || '')),
          map(value => this.filterPages(value || ''))
        );
        this.getPageName();
      }
    });
  }

  onChangeUpload(): void
  {
    const imageFormats = '.svg, .ico';
    const imagePermittedFileTypes = ['SVG', 'ICO'];
    const data: ImageUploaderModel = {
      round: false,
      maintainAspectRatio: false,
      aspectRatio: 4 / 2.5,
      acceptFormat: imageFormats,
      permittedFileTypes: imagePermittedFileTypes,
      maxSize: 100,
      hideRemoveButton: true,
      origImage: this.formControls.IconFile.value,
      imageServerUrl: this.formControls.IconUrl.value,
    };
    const imageUploaderDialog = this.dialog.open(ImageUploaderComponent, {
      width: '400px',
      panelClass: ['o-dialog-md-fullscreen'],
      data,
    });

    imageUploaderDialog.afterClosed().subscribe((result) =>
    {
      if (result)
      {
        this.formControls.IconFile.setValue(result?.origImage);
        this.formControls.IconUrl.setValue(result?.croppedImage);
        if (!result?.croppedImage)
        {
          this.formControls.Icon.setValue(null);
          this.formControls.IconFile.setValue(null);
          this.formControls.IconUrl.setValue(null);
        }
      }
    });
  }

  get formControls()
  {
    return this.form.controls;
  }

  savePage(): void
  {
    const formData = new FormData();
    if (this.pageData?.PageUID)
      formData.append('PageUID', this.pageData?.PageUID);
    formData.append('PageName', this.formControls?.PageName.value);
    formData.append('PageTitle', this.formControls?.PageTitle.value);
    formData.append('IconType', PageIconType.MatIcon.toString());
    if (this.formControls?.Icon.value)
    {
      const icon = this.formControls?.Icon.value;
      if (typeof icon !== 'string')
      {
        formData.append('Icon', `${ icon.value }:${ icon.iconStyle }:${ icon.fill }`);
      } else
      {
        formData.append('Icon', icon);
      }
    }
    formData.append('IconFile', this.formControls?.IconFile?.value);
    formData.append('PageType', this.formControls?.PageType?.value?.toString());
    if (this.formControls.Module.value?.ApplicationHierarchyUID)
      formData.append('ModuleApplicationHierarchyUID', this.formControls.Module.value?.ApplicationHierarchyUID);

    if (this.formControls.ContentPageExternalIdentifier.value?.ExternalIdentifier)
    {
      formData.append('ContentPageExternalIdentifier', this.formControls.ContentPageExternalIdentifier.value.ExternalIdentifier);
      formData.append('ContentPagePageType', this.formControls.ContentPageExternalIdentifier.value.PageType);
    }

    if (this.formControls.ExternalURL.value?.toString())
    {
      formData.append('ExternalURL', this.formControls.ExternalURL.value?.toString());
      formData.append('OpenExternalURLInNewTab', (this.formControls?.OpenExternalURLInNewTab.value ?? false).toString());
    }

    this.form.controls?.PageVisibilityRoles?.value?.forEach((item, index) =>
    {
      formData.append(`PageVisibilityRoles`, item?.toString());
    });

    formData.append('VisibleToAnonymous', this.formControls?.VisibleToAnonymous?.value?.toString());
    formData.append('DevicePageResponsiveSettings', JSON.stringify(this.devicePageResponsiveSettings));
    formData.append('ShouldUpdateStyleSheet', this.shouldUpdateStyleSheet.toString());
    formData.append('StyleSheet', this.styleSheet);

    this.service.PostPage(formData).subscribe({
      next: (response: PostPageResponse) =>
      {
        if (response)
        {
          this.snackBarService.showSuccess(this.translate.instant('MARKETING_CMS_ADMIN.MANAGE_PAGE_DIALOG.SUCCESS_MESSAGE'), '', null, 'left');
          const request = new GetPageRequest();
          request.PageUID = response.PageUID;
          this.service.GetPage(request).subscribe((response: GetPageResponse) =>
          {
            this.pageData = response;
            this.form.patchValue({
              PageName: this.pageData.PageName,
              PageTitle: this.pageData.PageTitle,
              IconUrl: this.pageData.IconUrl,
              PageType: this.pageData.PageType,
              Module: this.pageData.ApplicableModules?.find(
                (module: ApplicableModule) => module.ApplicationHierarchyUID === this.pageData.Module?.ModuleApplicationHierarchyUID
              ),
              ApplicableModules: this.pageData.ApplicableModules?.map(m => (m.ApplicationHierarchyUID)),
              ContentPageExternalIdentifier: this.pageData.Content,
              ExternalURL: this.pageData.ExternalURL?.ExternalURL,
              OpenExternalURLInNewTab: this.pageData.ExternalURL?.OpenExternalURLInNewTab,
              PageVisibilityRoles: this.pageData.PageVisibilityRoles,
              VisibleToAnonymous: this.pageData.VisibleToAnonymous
            });
            this.applicableModules = this.pageData.ApplicableModules;
            this.applicableParentPages = this.pageData.ApplicableParentPages.sort((a, b) => a.PageName.localeCompare(b.PageName));
            this.applicableParentPages.unshift({ PageUID: null, PageName: "" });
            this.teiChipData = this.pageData.ApplicableRoles?.map(m => ({ id: null, name: m.DesignationName + " Roles", active: true, children: m.Roles.map(q => ({ id: q.RoleUID, name: q.RoleName, active: true })) }));
            this.devicePageResponsiveSettings = this.pageData.DevicePageResponsiveSettings;
            this.deviceTypeChange(this.deviceType);

            if (this.pageData.PageType == this.pageType.Content && this.pageData.Content?.ContentPageExternalIdentifier)
            {
              this.form.get('ContentPageExternalIdentifier').patchValue(this.butterCmsPages.find(item => item.ExternalIdentifier == this.pageData.Content?.ContentPageExternalIdentifier));
            }
          });
        }
      }
    });
  }

  close(): void
  {
    this.dialogRef.close(true);
  }

  onPageStyleSheetDialog(): void
  {
    const dialogRef = this.dialog.open(PageStyleSheetDialogComponent, {
      panelClass: ['o-dialog-fullscreen'],
      data: {
        title: this.translate.instant('MARKETING_CMS_ADMIN.MANAGE_PAGE_DIALOG.PAGE_STYLESHEET'),
        pageUID: this.pageData.PageUID
      }
    });

    dialogRef.componentInstance.onDataSent.subscribe(data => {
      this.styleSheet = data?.StyleSheet;
      this.shouldUpdateStyleSheet = true;
      this.snackBarService.showSuccess(`${ this.translate.instant("MARKETING_CMS_ADMIN.MANAGE_PAGE_DIALOG.SUCCESS_MESSAGE") }`, '', null, 'left');
    });
  }
}
