import { Component, Input, OnInit } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup, FormGroupDirective } from '@angular/forms';

@Component({
  selector: 'tei-quill-container',
  templateUrl: './quill-container.component.html',
  styleUrls: ['./quill-container.component.scss'],
})
export class QuillContainerComponent implements OnInit
{
  @Input() ContentHeader: string;
  @Input() ContentDescription: string;
  @Input() ControlName: string;
  form: UntypedFormGroup;
  constructor(private rootFormGroup: FormGroupDirective) { }

  ngOnInit(): void
  {
    this.form = this.rootFormGroup?.control;
  }

  getFormControlByName(name: string): UntypedFormControl
  {
    return this.form.get(name) as UntypedFormControl;
  }
}
