import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { SnackBarService } from '@tei/material';
import { Subject, Subscription } from 'rxjs';
import {
  GetPageStyleSheetRequest,
  GetPageStyleSheetResponse
} from '../../../../@intermediate/models/get-page-style-sheet.model';
import { PostPageStyleSheetRequest } from '../../../../@intermediate/models/post-page-style-sheet-request.model';
import { IPageStyleSheetService } from '../../../../@intermediate/page-style-sheet.interface';

@Component({
  selector: 'tei-page-style-sheet-dialog',
  templateUrl: './page-style-sheet-dialog.component.html',
  styleUrls: ['./page-style-sheet-dialog.component.scss'],
})
export class PageStyleSheetDialogComponent implements OnInit, OnDestroy
{
  pageData: any;
  form: FormGroup = new FormGroup({
    stylesheet: new FormControl('')
  });
  private dataSubject = new Subject<any>();
  public onDataSent = this.dataSubject.asObservable();

  editorOptions = {
    language: 'css',
    automaticLayout: true,
    minimap: { enabled: false }
  };

  private subscriptions = new Subscription();

  constructor(
    private translate: TranslateService,
    @Inject('IPageStyleSheetService') private pageStyleSheetService: IPageStyleSheetService,
    public dialogRef: MatDialogRef<PageStyleSheetDialogComponent>,
    private snackBarService: SnackBarService,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) { }

  ngOnInit(): void
  {
    this.pageData = this.data;
    this.loadPageStyleSheet();
  }

  get formControls()
  {
    return this.form.controls;
  }

  private loadPageStyleSheet(): void
  {
    if (!this.pageData?.pageUID) return;

    const request: GetPageStyleSheetRequest = {
      PageUID: this.pageData?.pageUID
    };

    this.subscriptions.add(
      this.pageStyleSheetService.Get(request).subscribe({
        next: (res: GetPageStyleSheetResponse) =>
        {
          this.form.patchValue({
            stylesheet: res?.Stylesheet || ''
          });
        },
        error: (error) =>
        {
        }
      })
    );
  }

  savePage(): void
  {
    const request: PostPageStyleSheetRequest = {
      PageUID: this.pageData?.pageUID,
      Stylesheet: this.form.value.stylesheet || ''
    };
    if (!this.pageData?.pageUID)
    {
      this.dataSubject.next({
        StyleSheet: this.form.value.stylesheet || ''
      });
    } else
    {
      this.subscriptions.add(
        this.pageStyleSheetService.Post(request).subscribe({
          next: () =>
          {
            this.snackBarService.showSuccess(`${ this.translate.instant("MARKETING_CMS_ADMIN.MANAGE_PAGE_DIALOG.STYLESHEET_SAVED") }`, '', null, 'left');
          },
          error: (error) =>
          {
          }
        })
      );
    }
  }

  close(): void
  {
    this.dialogRef.close(false);
  }

  ngOnDestroy(): void
  {
    this.subscriptions.unsubscribe();
  }
}
