import { ChangeDetectorRef, Component, Inject, OnInit, ViewChild } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { MatPaginator, PageEvent } from '@angular/material/paginator';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ResponsiveService } from '@tei/core';
import { BreadCrumb, ChipItemModel, ChipOptionModel, ChipType, ConfirmDialogService, DialogData, SnackBarService } from '@tei/material';
import { Subscription } from 'rxjs';
import { Banner, DeleteBannerRequest, GetBannersRequest } from '../../../@intermediate/models/get-banner.model';
import { IBannerService } from '../../../@intermediate/banner.interface';
@Component({
  selector: 'tei-pages-banners',
  templateUrl: './banners.component.html',
  styleUrls: ['./banners.component.scss']
})
export class BannersComponent implements OnInit
{
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  subscription: Subscription = new Subscription();
  totalCount = 0;
  pageIndex = 0;
  pageSize = 10;
  isLoading = false;
  dataSource: Banner[] = [];
  getBannersRequest: GetBannersRequest = new GetBannersRequest();
  statuses = [
    { Status: this.translate.instant('MARKETING_CMS_ADMIN.TABLE.ENABLED'), Value: true },
    { Status: this.translate.instant('MARKETING_CMS_ADMIN.TABLE.DISABLED'), Value: false }];
  statusChipItemsWithDisplayName = [{ chipName: 'Status', displayName: this.translate.instant('MARKETING_CMS_ADMIN.TABLE.STATUS'), bindValue: 'Value' }];
  displayedColumns: string[] = ['BannerDetails', 'Status', 'Actions', 'MobileMode'];
  isSmallScreen = false;
  screenSize = '';
  innerWidth: number;
  get mobileMode(): boolean { return (this.innerWidth <= 960 || (this.screenSize === 'xs' || this.screenSize === 'sm')); }
  ChipType = ChipType;
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('MARKETING_CMS_ADMIN.BANNERS.MARKETING'),
      relativeUrl: ''
    },
    {
      title: this.translate.instant('MARKETING_CMS_ADMIN.CONTENT_MANAGEMENT.CONTENT_MANAGEMENT'),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant('MARKETING_CMS_ADMIN.BANNERS.BANNERS'),
      relativeUrl: ''
    }
  ];
  constructor(
    public dialog: MatDialog,
    private confirmdialogService: ConfirmDialogService,
    @Inject('IBannerService') private bannerService: IBannerService,
    private snackBarService: SnackBarService,
    private translate: TranslateService,
    private router: Router,
    private responsiveService: ResponsiveService,
    private changeDetectorRef: ChangeDetectorRef,
  ) { }

  ngOnInit(): void
  {
    this.loadBanners(this.getBannersRequest);
    this.handleScreenResize();
  }

  private handleScreenResize(): void
  {
    this.subscription.add(this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
    {
      this.innerWidth = window.innerWidth;
      this.screenSize = screenSize;
      this.isSmallScreen = screenSize === 'xs';
      this.changeDetectorRef.detectChanges();
    }));
  }

  handlePaginationEvent(event: PageEvent): void
  {
    this.pageIndex = event.pageIndex;
    this.pageSize = event.pageSize;
    this.loadBanners(this.getBannersRequest);
  }

  public loadBanners(request: GetBannersRequest, showFirstPage?: boolean): void
  {
    this.isLoading = true;
    this.getBannersRequest.PageSize = this.pageSize;
    this.getBannersRequest.PageNumber = this.pageIndex;

    if (showFirstPage)
    {
      this.pageIndex = 0;
    }

    this.getBannersRequest.PageNumber = this.pageIndex + 1;
    this.bannerService.Get(this.getBannersRequest).subscribe({
      next: (response) =>
      {
        this.dataSource = response.Banners;
        this.totalCount = response.ItemsCount;
      },
      error: (error) => { },
      complete: () =>
      {
        this.isLoading = false;
      }
    });
  }

  onStatusFilterChange(filterObjects: Array<ChipItemModel>): void
  {
    const chipItem = filterObjects[0];
    if (chipItem.value !== undefined)
    {
      this.getBannersRequest.Status = chipItem.value !== 'Disabled' && chipItem.value !== 'Dsactiv';
    }
    else
    {
      delete this.getBannersRequest.Status;
    }
    this.loadBanners(this.getBannersRequest);
  }

  onBannerIdFilterChange(chipOptionModel: ChipOptionModel): void
  {
    if (chipOptionModel.firstValue != undefined)
    {
      this.getBannersRequest.BannerIdentifier = chipOptionModel.firstValue;
    }
    else
    {
      delete this.getBannersRequest.BannerIdentifier;
    }
    this.loadBanners(this.getBannersRequest);
  }

  onTitleFilterChange(chipOptionModel: ChipOptionModel): void
  {
    if (chipOptionModel.firstValue != undefined)
    {
      this.getBannersRequest.Title = chipOptionModel.firstValue;
    }
    else
    {
      delete this.getBannersRequest.Title;
    }
    this.loadBanners(this.getBannersRequest);
  }

  openDeleteEventDialog(BannerUID: number): void
  {
    const dialogData: DialogData = {
      title: this.translate.instant('MARKETING_CMS_ADMIN.DELETE.TITLE'),
      message: this.translate.instant('MARKETING_CMS_ADMIN.DELETE.DESCRIPTION'),
      textOkayButton: this.translate.instant('MARKETING_CMS_ADMIN.DELETE.DELETE'),
      textCancelButton: this.translate.instant('MARKETING_CMS_ADMIN.DELETE.CANCEL'),
      buttonColor: 'warn'
    };
    const dialogRef = this.confirmdialogService.openDialog(dialogData, { disableClose: true });

    dialogRef.afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.deleteEvent(BannerUID);
      }
    });
  }

  deleteEvent(BannerUID: number): void
  {
    const request: DeleteBannerRequest = { BannerUID };

    this.isLoading = true;
    this.bannerService.DeleteBanner(request).subscribe(
      () =>
      {
        this.isLoading = false;
        this.snackBarService.showSuccess(this.translate.instant('MARKETING_CMS_ADMIN.DELETE.DELETE_BANNER_SUCCESS'), "", null, "left", "bottom");
        this.loadBanners(this.getBannersRequest);
      }
    );
  }
}
