export class GetResponsiveSettingRequest
{
  DeviceTypeID: DeviceType;
}

export class GetResponsiveSettingResponse
{
  DeviceTypeID: DeviceType;
  DeviceType: string;
  ResponsivePageSettings: Page[];
}

export class Page
{
  UID: string; //use for managing local changes in client-side
  PageUID: string;
  ParentPageUID: string;
  Level: number;
  PageName: string;
  SortOrder: number;
  Children: Page[];
}

export enum DeviceType
{
  Desktop = 1,
  Tablet = 2,
  Mobile = 3
}
