import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetContentPagesRequest, GetContentPagesResponse, GetPageRequest, GetPageResponse } from '../@intermediate/models/get-page.model';
import
{
  GetNavigationSettingRequest,
  GetNavigationSettingResponse
} from '../@intermediate/models/get-web-navigation.model';
import { PostPageResponse } from '../@intermediate/models/post-page.model';
import { ReorderWebNavigationRequest, ReorderWebNavigationResponse } from '../@intermediate/models/post-web-navigation-reorder.model';
import
{
  GetResponsiveSettingRequest,
  GetResponsiveSettingResponse
} from '../@intermediate/models/post-web-navigation-responsive-setting.model';
import
{
  PageDeleteRequest,
  PageDeleteResponse,
  PostNavigationSettingRequest,
  PostNavigationSettingResponse
} from '../@intermediate/models/post-web-navigation.model';
import { IWebNavigationService } from '../@intermediate/web-navigation.interface';

@Injectable({
  providedIn: 'root'
})
export class WebNavigationService implements IWebNavigationService
{
  private readonly serviceUrl: string;
  private readonly managePageUrl: string;
  private readonly contentPagesUrl: string;
  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Marketing/CMS/Admin/WebNavigation';
    this.managePageUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Marketing/CMS/Admin/Page';
    this.contentPagesUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Marketing/CMS/Admin/ContentPages';
  }

  Reorder(request: ReorderWebNavigationRequest): Observable<ReorderWebNavigationResponse>
  {
    const apiUrl = this.serviceUrl + "/Reorder";
    return this.http.post<PostNavigationSettingResponse>(apiUrl, request);
  }

  Get(request: GetNavigationSettingRequest): Observable<GetNavigationSettingResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.get<GetNavigationSettingResponse>(apiUrl, { params: request as any });
  }

  GetResponsiveSetting(request: GetResponsiveSettingRequest): Observable<GetResponsiveSettingResponse>
  {
    const apiUrl = this.serviceUrl + '/ResponsiveSetting';
    return this.http.get<GetResponsiveSettingResponse>(apiUrl, { params: request as any });
  }

  Post(request: PostNavigationSettingRequest): Observable<PostNavigationSettingResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.post<PostNavigationSettingResponse>(apiUrl, request);
  }

  GetPage(request: GetPageRequest): Observable<GetPageResponse>
  {
    const apiUrl = this.managePageUrl;
    return this.http.get<GetPageResponse>(apiUrl, { params: request as any });
  }

  PostPage(request: FormData): Observable<PostPageResponse>
  {
    const apiUrl = this.managePageUrl;
    return this.http.post<PostPageResponse>(apiUrl, request);
  }

  GetContentPages(request: GetContentPagesRequest): Observable<GetContentPagesResponse>
  {
    const apiUrl = this.contentPagesUrl;
    return this.http.get<GetContentPagesResponse>(apiUrl, { params: request as any });
  }
  DeletePage(request: PageDeleteRequest): Observable<PageDeleteResponse>
  {
    const apiUrl = this.managePageUrl + '/Delete';
    return this.http.post<PageDeleteResponse>(apiUrl, request);
  }

}
