import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { CMSAdminRoutingModule } from './cms-admin-routing.module';
import { CMSAdminSettingModule } from './cms-admin-setting.module';
import { CMSAdminComponent } from './cms-admin.component';
import { BannerSortOrderModule } from './pages/content-management/banners/banner-sort-order/banner-sort-order.module';
import { BannersModule } from './pages/content-management/banners/banners.module';
import { NewBannerModule } from './pages/content-management/banners/new-banner/new-banner.module';
import { ContentManagementModule } from './pages/content-management/content-management.module';
import { WebHomePageModule } from './pages/content-management/web-home-page/web-home-page.module';
import { WebNavigationModule } from './pages/content-management/web-navigation/web-navigation.module';
import { DefaultModule } from './pages/default/default.module';
import { PagesModule } from './pages/pages.module';


const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule, RouterModule];
const MODULES = [
  CMSAdminSettingModule,
  CMSAdminRoutingModule,
  PagesModule,
  DefaultModule,
  NewBannerModule,
  BannerSortOrderModule,
  BannersModule,
  ContentManagementModule,
  WebHomePageModule,
  WebNavigationModule
];

const COMPONENTS = [CMSAdminComponent];
const PIPES = [];

@NgModule({
  imports: [...BASE_MODULES, ...MODULES],
  declarations: [...COMPONENTS, ...PIPES],
  providers: []
})
export class CMSAdminModule
{ }
