import { FlatTreeControl } from '@angular/cdk/tree';
import { Component, ElementRef, Inject, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { MatTreeFlatDataSource, MatTreeFlattener } from '@angular/material/tree';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService, ResponsiveService } from '@tei/core';
import { BreadCrumb, ConfirmDialogService, DialogData, SnackBarService } from '@tei/material';
import { Subscription } from 'rxjs';
import { GetPageRequest, GetPageResponse } from '../../../@intermediate/models/get-page.model';
import
{
  AcceptableDesktopNavigationType, ApplicablePageForHomePage, DeviceType,
  GetNavigationSettingRequest,
  GetNavigationSettingResponse,
  WebNavigationForm
} from '../../../@intermediate/models/get-web-navigation.model';
import { PostHomePageSettingResponse } from '../../../@intermediate/models/post-web-home-page.model';
import { DropOverAreaType, ReorderWebNavigationRequest } from '../../../@intermediate/models/post-web-navigation-reorder.model';
import
{
  GetResponsiveSettingRequest,
  GetResponsiveSettingResponse, Page
} from '../../../@intermediate/models/post-web-navigation-responsive-setting.model';
import { PageDeleteRequest, PageDeleteResponse, PostNavigationSettingRequest } from '../../../@intermediate/models/post-web-navigation.model';
import { IWebNavigationService } from '../../../@intermediate/web-navigation.interface';
import { FlatNode, TreeListMenuActionsService } from '../../../@services/tree-list-menu-actions.service';
import { ManagePageDialogComponent } from './manage-page-dialog/manage-page-dialog.component';

@Component({
  selector: 'tei-web-navigation',
  templateUrl: './web-navigation.component.html',
  styleUrls: ['./web-navigation.component.scss'],
})
export class WebNavigationComponent implements OnInit, OnDestroy
{
  @ViewChild('emptyItem') emptyItem: ElementRef;
  subscription = new Subscription();
  desktopNavigationTypes: AcceptableDesktopNavigationType[];
  applicablePagesForHomePage: ApplicablePageForHomePage[];
  enableCustomizeResponsiveBreakpoints: boolean;
  defaultDesktopCustomizedWidth = 1100;
  defaultTabletCustomizedWidth = 650;
  DeviceType = DeviceType;
  deviceType: DeviceType;
  treeControl: FlatTreeControl<FlatNode>;
  treeFlattener: MatTreeFlattener<Page, FlatNode>;
  dataSource: MatTreeFlatDataSource<Page, FlatNode>;
  getLevel = (node: FlatNode) => node.Level;
  isExpandable = (node: FlatNode) => node.expandable;
  getChildren = (node: Page): Page[] => node.Children;
  hasChild = (_: number, _nodeData: FlatNode) => _nodeData.expandable;
  flatNodeMap = new Map<FlatNode, Page>();
  nestedNodeMap = new Map<Page, FlatNode>();
  pages: Page[] = [];
  enableCustomNavigation: boolean;
  isMobile: boolean;
  dropOverAreaType = DropOverAreaType;

  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('MARKETING_CMS_ADMIN.WEB_NAVIGATION.MARKETING'),
      relativeUrl: ''
    },
    {
      title: this.translate.instant('MARKETING_CMS_ADMIN.WEB_NAVIGATION.CONTENT_MANAGEMENT'),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant('MARKETING_CMS_ADMIN.WEB_NAVIGATION.WEB_NAVIGATION'),
      relativeUrl: ''
    }
  ];

  form: FormGroup<WebNavigationForm> = new FormGroup<WebNavigationForm>({
    EnableCustomNavigation: new FormControl(false),
    DesktopNavigationType: new FormControl(null),
    HomePageUID: new FormControl(null),
    DeviceType: new FormControl(DeviceType.Desktop),
    EnableCustomizeResponsiveBreakpoints: new FormControl(false),
    DesktopCustomizedWidth: new FormControl(this.defaultDesktopCustomizedWidth),
    TabletCustomizedWidth: new FormControl(this.defaultTabletCustomizedWidth),
  });

  constructor(private translate: TranslateService,
    public manageLoadingService: ManageLoadingService,
    private responsiveService: ResponsiveService,
    public dialog: MatDialog,
    @Inject('IWebNavigationService') private service: IWebNavigationService,
    @Inject('DatabaseTemp') private databaseTemp: TreeListMenuActionsService,
    @Inject('Database') private database: TreeListMenuActionsService,
    private confirmdialogService: ConfirmDialogService,
    private snackBarService: SnackBarService)
  {

  }

  ngOnInit(): void
  {
    this.configureTree();
    this.getWebNavigation();
    this.onEnableCustomizeResponsiveBreakpoints();
    this.onEnableCustomNavigation();
    this.isSmallScreen();
  }

  private configureTree(): void
  {
    this.treeFlattener = new MatTreeFlattener(this.transformer, this.getLevel, this.isExpandable, this.getChildren);
    this.treeControl = new FlatTreeControl<FlatNode>(this.getLevel, this.isExpandable);
    this.dataSource = new MatTreeFlatDataSource(this.treeControl, this.treeFlattener);
    this.database.dataChange.subscribe(data =>
    {
      this.dataSource.data = [];
      this.dataSource.data = data;
    });
  }

  transformer = (node: Page, level: number) =>
  {
    const existingNode = this.nestedNodeMap.get(node);
    const flatNode = existingNode && existingNode.PageName === node.PageName
      ? existingNode
      : new FlatNode();
    flatNode.PageName = node.PageName;
    flatNode.Level = level;
    flatNode.ParentPageUID = node.ParentPageUID;
    flatNode.PageUID = node.PageUID;
    flatNode.expandable = (node.Children && node.Children.length > 0);
    this.flatNodeMap.set(flatNode, node);
    this.nestedNodeMap.set(node, flatNode);
    return flatNode;
  };

  setOrder(categories: Page[])
  {
    let orderNo = 0;
    categories?.forEach(c =>
    {
      c.SortOrder = orderNo++;
      this.setOrder(c?.Children);
    });
  }

  protected isSmallScreen(): boolean
  {
    const screenSize = this.responsiveService.mediaBreakpoint.getValue();
    this.isMobile = screenSize === 'xs';
    return this.isMobile;
  }

  getResponsiveSettingByDeviceTypeID(deviceType): void
  {
    this.deviceType = deviceType;
    const request = new GetResponsiveSettingRequest();
    request.DeviceTypeID = deviceType;
    this.subscription.add(this.service.GetResponsiveSetting(request).subscribe((response: GetResponsiveSettingResponse) =>
    {
      if (response)
      {
        this.pages = response.ResponsivePageSettings;
        this.assignUIDs(this.pages);

        this.database.initialize(this.pages);
        this.database.reviseSortOrder();

        this.databaseTemp.initialize(JSON.parse(JSON.stringify(this.pages)));
        this.databaseTemp.reviseSortOrder();
      }
    }));
  }

  assignUIDs(pages: Page[])
  {
    pages.forEach(item =>
    {
      item.UID = this.database.getGuid();

      if (item.Children && item.Children.length > 0)
      {
        this.assignUIDs(item.Children);
      }
    });
  }

  onEnableCustomNavigation(): void
  {
    this.form.get('EnableCustomNavigation').valueChanges.subscribe((isEnable: boolean) =>
    {
      this.enableCustomNavigation = !isEnable;
    });
  }


  onEnableCustomizeResponsiveBreakpoints(): void
  {
    this.form.get('EnableCustomizeResponsiveBreakpoints').valueChanges.subscribe((isEnable: boolean) =>
    {
      this.enableCustomizeResponsiveBreakpoints = !isEnable;
      const DesktopCustomizedWidth = this.form.get('DesktopCustomizedWidth');
      const TabletCustomizedWidth = this.form.get('TabletCustomizedWidth');
      if (isEnable)
      {
        DesktopCustomizedWidth.setValidators([Validators.required, Validators.min(1), Validators.pattern(/^-?(0|[1-9]\d*)?$/)]);
        TabletCustomizedWidth.setValidators([Validators.required, Validators.min(1), Validators.pattern(/^-?(0|[1-9]\d*)?$/)]);
      } else
      {
        DesktopCustomizedWidth.setValue(this.defaultDesktopCustomizedWidth);
        TabletCustomizedWidth.setValue(this.defaultTabletCustomizedWidth);
      }
      DesktopCustomizedWidth.updateValueAndValidity();
      TabletCustomizedWidth.updateValueAndValidity();
    });
  }

  private getWebNavigation(): void
  {
    this.manageLoadingService.startLoading();

    const request = new GetNavigationSettingRequest();
    this.subscription.add(this.service.Get(request).subscribe((response: GetNavigationSettingResponse) =>
    {
      if (response)
      {
        this.manageLoadingService.finishLoading();
        this.form.setValue({
          EnableCustomNavigation: response.EnableCustomNavigation,
          DesktopNavigationType: response.DesktopNavigationType,
          HomePageUID: response.HomePageUID,
          EnableCustomizeResponsiveBreakpoints: response.EnableCustomizeResponsiveBreakpoints,
          DesktopCustomizedWidth: response.DesktopCustomizedWidth,
          TabletCustomizedWidth: response.TabletCustomizedWidth,
          DeviceType: DeviceType.Desktop
        });
        this.desktopNavigationTypes = response.DesktopNavigationTypes;
        this.applicablePagesForHomePage = response.ApplicablePagesForHomePage;
        this.getResponsiveSettingByDeviceTypeID(DeviceType.Desktop);
      }
    }));
  }

  onNewPage(): void
  {
    const request = new GetPageRequest();

    this.service.GetPage(request).subscribe((response: GetPageResponse) =>
    {
      const dialogRef = this.dialog.open(ManagePageDialogComponent, {
        panelClass: ['o-dialog-fullscreen'],
        data: {
          deviceType: this.deviceType,
          title: this.translate.instant('MARKETING_CMS_ADMIN.MANAGE_PAGE_DIALOG.TITLE'),
          pageData: response
        }
      });
      dialogRef.afterClosed().subscribe(result =>
      {
        if (result)
        {
          this.getResponsiveSettingByDeviceTypeID(this.deviceType);
        }
      });
    });
  }

  managePage(node?: any): void
  {
    const request = new GetPageRequest();
    request.PageUID = node.PageUID;

    this.service.GetPage(request).subscribe((response: GetPageResponse) =>
    {
      const dialogRef = this.dialog.open(ManagePageDialogComponent, {
        panelClass: ['o-dialog-fullscreen'],
        data: {
          deviceType: this.deviceType,
          title: this.translate.instant('MARKETING_CMS_ADMIN.MANAGE_PAGE_DIALOG.TITLE'),
          pageData: response
        }
      });
      dialogRef.afterClosed().subscribe(result =>
      {
        if (result)
        {
          this.getResponsiveSettingByDeviceTypeID(this.deviceType);
        }
      });
    });
  }

  deviceTypeChange(deviceType): void
  {
    this.getResponsiveSettingByDeviceTypeID(deviceType.value);
  }

  get formControls()
  {
    return this.form.controls;
  }

  removePage(node: any): void
  {
    const dialogData: DialogData = {
      title: this.translate.instant('MARKETING_CMS_ADMIN.MANAGE_PAGE_DIALOG.DELETE.TITLE'),
      message: this.translate.instant('MARKETING_CMS_ADMIN.MANAGE_PAGE_DIALOG.DELETE.DESCRIPTION'),
      textOkayButton: this.translate.instant('MARKETING_CMS_ADMIN.MANAGE_PAGE_DIALOG.DELETE.DELETE'),
      textCancelButton: this.translate.instant('MARKETING_CMS_ADMIN.MANAGE_PAGE_DIALOG.DELETE.CANCEL'),
      buttonColor: 'warn'
    };
    const dialogRef = this.confirmdialogService.openDialog(dialogData, { maxWidth: '300px' });

    dialogRef.afterClosed().subscribe(result =>
    {
      if (result)
      {
        const request = new PageDeleteRequest();
        request.PageUID = node.PageUID;
        this.service.DeletePage(request).subscribe({
          next: (response: PageDeleteResponse) =>
          {
            if (response)
            {
              this.snackBarService.showSuccess(this.translate.instant('MARKETING_CMS_ADMIN.WEB_NAVIGATION.DELETE_MESSAGE'), '', null, 'left');
              this.getResponsiveSettingByDeviceTypeID(this.deviceType);
            }
          }
        });
      }
    });
  }

  onSave(): void
  {
    const request = new PostNavigationSettingRequest();
    request.EnableCustomNavigation = this.formControls.EnableCustomNavigation.value;
    request.DesktopNavigationType = this.formControls.DesktopNavigationType.value;
    request.HomePageUID = this.formControls.HomePageUID.value;
    request.EnableCustomizeResponsiveBreakpoints = this.formControls.EnableCustomizeResponsiveBreakpoints.value;
    request.TabletCustomizedWidth = this.formControls.TabletCustomizedWidth.value;
    request.DesktopCustomizedWidth = this.formControls.DesktopCustomizedWidth.value;

    this.service.Post(request).subscribe({
      next: (response: PostHomePageSettingResponse) =>
      {
        if (response)
        {
          this.snackBarService.showSuccess(this.translate.instant('MARKETING_CMS_ADMIN.WEB_NAVIGATION.SUCCESS_MESSAGE'), '', null, 'left');
          this.manageLoadingService.finishLoading();
        }
      }
    });
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }

  /* Drag and drop */
  dragNode: FlatNode;
  dragNodeExpandOverWaitTimeMs = 500;
  dragNodeExpandOverNode: FlatNode;
  dragNodeExpandOverTime: number;
  dragNodeExpandOverArea: DropOverAreaType;
  lastDragOverTarget: Element;

  handleDragStart(event: any, node: FlatNode): void
  {
    if (event.dataTransfer)
      event.dataTransfer.setDragImage(this.emptyItem.nativeElement, 0, 0);
    this.dragNode = node;
    this.treeControl.collapse(node);

    const targetElement = event.target as HTMLElement;
    targetElement.classList.add('dragged');
  }

  handleDragOver(event: any, node: FlatNode): void
  {
    event.preventDefault();
    // Handle node expand
    if (node === this.dragNodeExpandOverNode)
    {
      //for now we disabled auto expand on hover
      // if (this.dragNode !== node && !this.treeControl.isExpanded(node))
      // {
      //   if ((new Date().getTime() - this.dragNodeExpandOverTime) > this.dragNodeExpandOverWaitTimeMs)
      //   {
      //     this.treeControl.expand(node);
      //   }
      // }
    } else
    {
      this.dragNodeExpandOverNode = node;
      this.dragNodeExpandOverTime = new Date().getTime();
    }


    let isValidToBeBelow = false;
    let currentNode = this.flatNodeMap.get(node);
    let parent = this.database.getParentFromNodes(currentNode);
    if (parent == null)
    {
      let items = this.database.data;
      isValidToBeBelow = items.indexOf(currentNode) == items.length - 1;
    } else
    {
      isValidToBeBelow = parent.Children.indexOf(currentNode) == parent.Children.length - 1;
    }

    // Handle drag area
    const target = event.currentTarget || event.target;
    let percentageY = event.offsetY / target.clientHeight;
    let centerThreshold = 0.75;
    if (!isValidToBeBelow)
      centerThreshold = 1.0;

    if (percentageY < 0.25)
    {
      this.dragNodeExpandOverArea = DropOverAreaType.Above;
    }
    else if (percentageY >= 0.25 && percentageY <= centerThreshold)
    {
      this.dragNodeExpandOverArea = DropOverAreaType.Center;
    }
    else if (percentageY > 0.75 && percentageY <= 1 && isValidToBeBelow)
    {
      this.dragNodeExpandOverArea = DropOverAreaType.Below;
    }
    else
    {
      this.dragNodeExpandOverArea = null;
    }
  }

  handleDrop(event: any, node: FlatNode): void
  {
    event.preventDefault();

    if (node !== this.dragNode)
    {
      let request: ReorderWebNavigationRequest = {
        deviceType: this.deviceType,
        pageUID: this.flatNodeMap.get(this.dragNode).PageUID,
        newSortOrder: 0
      };
      let newItem: Page;

      let currentNode = JSON.parse(JSON.stringify(this.flatNodeMap.get(node)));

      if (this.dragNodeExpandOverArea === DropOverAreaType.Above)
      {
        request.parentPageUID = this.flatNodeMap.get(node).ParentPageUID;
        newItem = this.databaseTemp.copyPasteItemAbove(this.flatNodeMap.get(this.dragNode), currentNode);
      }
      else if (this.dragNodeExpandOverArea === DropOverAreaType.Below)
      {
        request.parentPageUID = this.flatNodeMap.get(node).ParentPageUID;
        newItem = this.databaseTemp.copyPasteItemBelow(this.flatNodeMap.get(this.dragNode), currentNode);
      }
      else
      {
        request.parentPageUID = this.flatNodeMap.get(node).PageUID;
        newItem = this.databaseTemp.copyPasteItem(this.flatNodeMap.get(this.dragNode), currentNode);
      }
      this.databaseTemp.deleteItem(this.flatNodeMap.get(this.dragNode));
      var newSortPlace = this.databaseTemp.reviseSortOrderAndGetSortNumber(newItem);
      request.newSortOrder = newSortPlace;

      this.service.Reorder(request).subscribe({
        next: response =>
        {
          let newItem: Page;
          if (this.dragNodeExpandOverArea === DropOverAreaType.Above)
          {
            newItem = this.database.copyPasteItemAbove(this.flatNodeMap.get(this.dragNode), this.flatNodeMap.get(node));
          }
          else if (this.dragNodeExpandOverArea === DropOverAreaType.Below)
          {
            newItem = this.database.copyPasteItemBelow(this.flatNodeMap.get(this.dragNode), this.flatNodeMap.get(node));
          }
          else
          {
            newItem = this.database.copyPasteItem(this.flatNodeMap.get(this.dragNode), this.flatNodeMap.get(node));
          }

          this.database.deleteItem(this.flatNodeMap.get(this.dragNode));
          this.database.reviseSortOrder();
          // this.treeControl.expandDescendants(this.nestedNodeMap.get(newItem));
        },
        error: () =>
        {
          this.resetDragState();
        },
        complete: () =>
        {
          this.databaseTemp.initialize(JSON.parse(JSON.stringify(this.database.data)));
          this.databaseTemp.reviseSortOrder();
          this.resetDragState();
        }
      });
    } else
    {
      this.resetDragState();
    }
  }

  handleDragEnd(event: DragEvent): void
  {
    const targetElement = event.target as HTMLElement;
    targetElement.classList.remove('dragged');
    if (event.dataTransfer.dropEffect == "none")
      this.resetDragState();
  }

  resetDragState()
  {
    this.dragNode = null;
    this.dragNodeExpandOverNode = null;
    this.dragNodeExpandOverArea = null;
    this.dragNodeExpandOverTime = 0;
  }

  handleTouchStart(event: TouchEvent, node: FlatNode): void
  {
    if ((event.target as HTMLElement).closest('.a-tree-node-item-expand-toggle'))
    {
      return;
    }
    event.preventDefault();

    const touch = event.touches[0];
    const targetElement = document.elementFromPoint(touch.clientX, touch.clientY);

    if (targetElement)
    {
      const dragStartEvent = new CustomEvent('dragstart', {
        bubbles: true,
        detail: { node },
      });
      targetElement.dispatchEvent(dragStartEvent);
    }

    this.lastDragOverTarget = null;
  }

  handleTouchMove(event: TouchEvent, node: FlatNode): void
  {
    event.preventDefault();

    const touch = event.touches[0];
    const targetElement = document.elementFromPoint(touch.clientX, touch.clientY);

    if (targetElement)
    {
      const boundingRect = targetElement.getBoundingClientRect();
      const offsetY = touch.clientY - boundingRect.top;

      if (this.lastDragOverTarget !== targetElement)
      {
        if (this.lastDragOverTarget)
        {
          const dragLeaveEvent = new CustomEvent('dragleave', { bubbles: true });
          this.lastDragOverTarget.dispatchEvent(dragLeaveEvent);
        }

        const dragOverEvent = new CustomEvent('dragover', { bubbles: true });
        Object.assign(dragOverEvent, { offsetY, node });
        targetElement.dispatchEvent(dragOverEvent);

        this.lastDragOverTarget = targetElement;
      }
    }
  }

  handleTouchEnd(event: TouchEvent, node: FlatNode): void
  {
    if ((event.target as HTMLElement).closest('.a-tree-node-item-expand-toggle'))
    {
      return;
    }

    const touch = event.changedTouches[0];
    const targetElement = document.elementFromPoint(touch.clientX, touch.clientY);

    if (targetElement)
    {
      const dropEvent = new CustomEvent('drop', { bubbles: true });
      Object.assign(dropEvent, { node });
      targetElement.dispatchEvent(dropEvent);
    }

    this.lastDragOverTarget = null;
  }
}
