import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { ChangeDetectorRef, Component, Inject, OnInit, ViewChild } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { MatTable } from '@angular/material/table';
import { TranslateService } from '@ngx-translate/core';
import { ResponsiveService } from '@tei/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { Subscription } from 'rxjs';
import { IBannerService } from '../../../../@intermediate/banner.interface';
import { Banner, ReorderBannersRequest } from '../../../../@intermediate/models/get-banner.model';

@Component({
  selector: 'tei-cms-admin-banner-sort-order',
  templateUrl: './banner-sort-order.component.html',
  styleUrls: ['./banner-sort-order.component.scss']
})
export class BannerSortOrderComponent implements OnInit
{
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('MARKETING_CMS_ADMIN.BANNERS.MARKETING'),
      relativeUrl: ''
    },
    {
      title: this.translate.instant('MARKETING_CMS_ADMIN.CONTENT_MANAGEMENT.CONTENT_MANAGEMENT'),
      relativeUrl: '../../'
    },
    {
      title: this.translate.instant('MARKETING_CMS_ADMIN.BANNERS.BANNERS'),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant('MARKETING_CMS_ADMIN.BANNERS.SORT_ORDER'),
      relativeUrl: ''
    }
  ];

  dragDisabled: boolean = true;
  isLoading: boolean = false;
  dataSource: Banner[] = [];
  @ViewChild('table') table: MatTable<any>;
  subscription: Subscription = new Subscription();
  displayedColumns: string[] = ['BannerDetails', 'Status', 'Actions', 'MobileMode'];
  isSmallScreen = false;
  screenSize = '';
  innerWidth: number;
  get mobileMode(): boolean { return (this.innerWidth <= 960 || (this.screenSize === 'xs' || this.screenSize === 'sm')); }

  constructor(
    public dialog: MatDialog,
    private snackBarService: SnackBarService,
    @Inject('IBannerService') private bannerService: IBannerService,
    private translate: TranslateService,
    private responsiveService: ResponsiveService,
    private changeDetectorRef: ChangeDetectorRef,
  ) { }

  ngOnInit(): void
  {
    this.loadBanners();
    this.handleScreenResize();
  }

  public loadBanners(): void
  {
    this.isLoading = true;
    this.bannerService.GetAll().subscribe({
      next: (response) =>
      {
        this.dataSource = response.Banners;
      },
      error: (error) => { },
      complete: () =>
      {
        this.isLoading = false;
      }
    });
  }

  drop(event: CdkDragDrop<any[]>): void
  {
    const movedBanner = this.dataSource[event.previousIndex];
    moveItemInArray(this.dataSource, event.previousIndex, event.currentIndex);
    this.table.renderRows();
    const reorderRequest: ReorderBannersRequest = {
      BannerUID: movedBanner.BannerUID,
      NewSortOrder: event.currentIndex + 1
    };

    this.updateBannerOrder(reorderRequest);
  }

  private updateBannerOrder(request: ReorderBannersRequest): void
  {
    this.bannerService.ReorderBanners(request).subscribe({
      next: (response) =>
      {
        this.snackBarService.showSuccess(this.translate.instant('MARKETING_CMS_ADMIN.BANNER_SORT_ORDER.ORDER_UPDATED_SUCCESS'), "", null, "left", "bottom");
      }
    });
  }

  private handleScreenResize(): void
  {
    this.subscription.add(this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
    {
      this.innerWidth = window.innerWidth;
      this.screenSize = screenSize;
      this.isSmallScreen = screenSize === 'xs';
      this.changeDetectorRef.detectChanges();
    }));
  }

}
