import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule } from '@tei/material';
import { SecureNavigationModule } from '@tei/material-smart-components';
import { CMSAdminSettingComponent } from './cms-admin-setting.component';
import { DefaultModule } from './pages/default/default.module';
import { PagesModule } from './pages/pages.module';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule, RouterModule];
const MODULES = [
  PagesModule,
  DefaultModule,
];
const TEI_MATERIAL_MODULES = [
  BreadCrumbModule,
];
const TEI_MATERIAL_SMART_COMPONENTS_MODULES = [
  SecureNavigationModule
];
const TEI_CORE_MODULES = [
  TeiTextTranslatePipeModule
];

const COMPONENTS = [CMSAdminSettingComponent];
const PIPES = [];

@NgModule({
  declarations: [...COMPONENTS, ...PIPES],
  imports: [
    ...BASE_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_MATERIAL_SMART_COMPONENTS_MODULES,
    ...TEI_CORE_MODULES,
    ...MODULES
  ],
  exports: [
    ...COMPONENTS
  ],
})
export class CMSAdminSettingModule
{ }
