import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RouteData } from '@tei/core';
import { ContentManagementComponent } from './content-management/content-management.component';
import { PagesComponent } from './pages.component';

const routes: Routes = [
  {
    path: '',
    component: PagesComponent,
    children: [
      {
        path: '',
        component: ContentManagementComponent,
      },
      {
        path: 'banners',
        loadChildren: () => import('./content-management/banners/banners.module').then(m => m.BannersModule),
        data: { RouteData: new RouteData({ Title: 'BANNERS' }) },
      },
      {
        path: 'web-home-page',
        loadChildren: () => import('./content-management/web-home-page/web-home-page.module').then(m => m.WebHomePageModule),
        data: { RouteData: new RouteData({ Title: 'WEB_HOME_PAGE' }) },
      },
      {
        path: 'web-navigation',
        loadChildren: () => import('./content-management/web-navigation/web-navigation.module').then(m => m.WebNavigationModule),
        data: { RouteData: new RouteData({ Title: 'WEB_HOME_PAGE' }) },
      },
      {
        path: 'theme',
        loadChildren: () => import('./content-management/manage-user-theme/manage-user-theme.module').then(m => m.ManageUserThemeModule),
        data: { RouteData: new RouteData({ Title: 'THEME' }) },
      },
      { path: '**', redirectTo: '' }
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class PagesRoutingModule
{
}
