export class GetBannersRequest {
  BannerIdentifier?: string;
  Title?: string;
  Status?: boolean;
  PageNumber: number;
  PageSize: number;
}

export interface DeleteBannerRequest {
  BannerUID: number;
}

export class GetBannersResponse {
  Banners: Banner[];
  ItemsCount: number;
}

export class Banner {
  BannerUID: string;
  BannerIdentifier: string;
  Title: string;
  StartDateTime: string;
  EndDateTime: string;
  Status: boolean;
}
export class GetBannerRequest {
  BannerUID: string;
}

export class GetBannerResponse
{
  BannerUID: string;
  BannerIdentifier: string;
  Title: string;
  Message: string;
  TriggerType: BannerTriggerType;
  Pages: ApplicablePage[];
  StartDateTime: string;
  EndDateTime: string;
  StartTime?: Date;
  EndTime?: Date;
  DisabledOnMobile: boolean;
  DisabledOnTablet: boolean;
  DisabledOnDesktop: boolean;
  StopShowingAfterDismiss: boolean;
  DisplayType: BannerDisplayType;
  Status: boolean;
  ApplicablePages: ApplicablePage[];
  ApplicableTriggerTypes: ApplicableTriggerType[];
  ApplicableDisplayTypes: ApplicableDisplayType[];
}


export class ApplicablePage {
  PageUID: string;
  PageName: string;
  PageType: ApplicablePageType;
}

export class ApplicableTriggerType {
  triggerTypeID: BannerTriggerType;
  triggerTypeDescription: string;
}

export class ApplicableDisplayType {
  displayTypeID: BannerDisplayType;
  displayTypeDescription: string;
}

export enum BannerDisplayType {
  TopBar = 1,
  CenterPopup = 2
}

export enum BannerTriggerType {
  PageLoad = 1
}

export enum ApplicablePageType {
  Route = 1,
  CMS = 2
}

export enum StatusEnum
{
  Enabled = 'Enabled',
  Disabled = 'Disabled'
}


export interface ReorderBannersRequest {
  BannerUID: string;
  NewSortOrder: number;
}

export interface ReorderBannersResponse {
  Banners: Banner[];
  ItemsCount: number;
}

