import { DragDropModule } from '@angular/cdk/drag-drop';
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatLegacyPaginatorModule as MatPaginatorModule } from '@angular/material/legacy-paginator';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';
import { MatSortModule } from '@angular/material/sort';
import { ResponsiveTableHeightModule } from '@tei/core';
import
{
  TeiDateTranslateModule,
  TeiTextTranslatePipeModule,
} from '@tei/core-translate';
import
{
  BreadCrumbModule,
  FilterChipsModule,
  FormTimePickerModule,
  LoadingModule,
  MultipleSelectChipsModule,
  QuickSearchFilterChipModule
} from '@tei/material';
import { DefaultTabLayoutModule } from '@tei/theme';
import { BannerService } from '../../../@services/banner.service';
import { QuillContainerModule } from '../../../components/quill-container/quill-container.module';
import { BannersRoutingModule } from './banners-routing.module';
import { BannersComponent } from './banners.component';

const BASE_MODULES = [FormsModule, CommonModule, ReactiveFormsModule, BannersRoutingModule];
const MATERIAL_MODULES = [
  MatCardModule,
  MatButtonModule,
  MatPaginatorModule,
  LoadingModule,
  MatCheckboxModule,
  MatSortModule,
  MatTableModule,
  MatInputModule,
  MatRadioModule,
  MatInputModule,
  MatMenuModule,
  MatTooltipModule
];
const TEI_MATERIAL_MODULES = [
  BreadCrumbModule,
  MatTableModule,
  FormTimePickerModule,
  FilterChipsModule,
  DragDropModule,
  MultipleSelectChipsModule,
];
const TEI_MARKETING_CMS_ADMIN_MODULES = [QuillContainerModule];
const TEI_CORE_MODULES = [
  TeiTextTranslatePipeModule,
  QuickSearchFilterChipModule,
  TeiDateTranslateModule,
  DefaultTabLayoutModule,
  ResponsiveTableHeightModule
];
const COMPONENTS = [
  BannersComponent
];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_MARKETING_CMS_ADMIN_MODULES,
    ...TEI_CORE_MODULES,
  ],
  providers: [{ provide: 'IBannerService', useClass: BannerService }],
})
export class BannersModule { }
