import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { TranslateModule } from '@ngx-translate/core';
import { TeiTextTranslatePipeModule } from "@tei/core-translate";
import { MaterialCodeEditorModule } from '@tei/material';
import { PageStyleSheetDialogComponent } from './page-style-sheet-dialog.component';
import { PageStyleSheetService } from '../../../../@services/page-style-sheet.service';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatButtonModule, MatDialogModule, MatDividerModule, MatIconModule, MaterialCodeEditorModule];
const COMPONENTS = [PageStyleSheetDialogComponent];
const TEI_CORE_MODULES = [
  TeiTextTranslatePipeModule,
  TranslateModule
];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_CORE_MODULES
  ],
  providers: [{ provide: 'IPageStyleSheetService', useClass: PageStyleSheetService }]
})
export class PageStyleSheetDialogModule { }
