import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatButtonToggleModule } from '@angular/material/button-toggle';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyOptionModule } from '@angular/material/legacy-core';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacySelectModule } from '@angular/material/legacy-select';
import { MatLegacySlideToggleModule as MatSlideToggleModule } from '@angular/material/legacy-slide-toggle';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import
{
  AvatarModule,
  BreadCrumbModule,
  LoadingModule
} from '@tei/material';
import { WebHomePageService } from '../../../@services/web-home-page.service';
import { WebHomePageRoutingModule } from './web-home-page-routing.module';
import { WebHomePageComponent } from './web-home-page.component';

const COMPONENTS = [WebHomePageComponent];
const BASE_MODULES = [FormsModule, CommonModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatCardModule, MatButtonModule, LoadingModule, MatInputModule, MatButtonToggleModule];
const TEI_MATERIAL_MODULES = [BreadCrumbModule, AvatarModule, WebHomePageRoutingModule, MatSlideToggleModule, MatLegacyOptionModule,
  MatLegacySelectModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];
@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES
  ],
  providers: [{ provide: 'IWebHomePageService', useClass: WebHomePageService }]
})
export class WebHomePageModule { }
