import { Component, OnInit } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NumericPadDataConfig, UserIdTextFieldConfig } from '@tei/material';

@Component({
  selector: 'tei-retail-admin-pages-default',
  templateUrl: './default.component.html',
  styleUrls: ['./default.component.scss']
})
export class DefaultComponent implements OnInit
{
  // Start Numeric

  userIdTextFieldConfig: UserIdTextFieldConfig = {
    minLenght: 3,
    maxLenght: 5
  };
  numericPadDataConfig: NumericPadDataConfig = {
    loginIcon: 'login',
    backspaceIcon: 'backspace',
    userIdInputConfig: {
      label: 'User ID',
      placeholder: 'Enter or Swipe ID',
      validationMessage: {
        required: 'User ID is required.',
        onlyNumber: 'User ID must be a number.',
        minLength: `Please enter at least ${ this.userIdTextFieldConfig.minLenght } digits.`,
        maxLength: `Please enter no more than  ${ this.userIdTextFieldConfig.maxLenght } digits.`
      }
    }
  };

  login(userLoginData)
  {
    console.log(`User #${ userLoginData.userId } logged in.`);
  }

  // End Numeric

  // Start Quick Search
  form: UntypedFormGroup;

  constructor(private formBuilder: UntypedFormBuilder, private router: Router)
  {
  }

  ngOnInit()
  {
    this.form = this.formBuilder.group({
      quickSearch: [null, [Validators.required]]
    });
  }

  onItemClick(item: any): void
  {
    console.log('quick search on item click', item);
  }
  // End Quick Search

  logout(): void
  {
    this.router.navigate(['pages/booking/event/logout']);
  }
}
