import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { ReactiveFormsModule } from '@angular/forms';
import { MatButtonToggleModule } from '@angular/material/button-toggle';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyOptionModule } from '@angular/material/legacy-core';
import { MatLegacyFormFieldModule } from '@angular/material/legacy-form-field';
import { MatLegacyInputModule } from '@angular/material/legacy-input';
import { MatLegacySelectModule } from '@angular/material/legacy-select';
import { MatLegacySlideToggleModule } from '@angular/material/legacy-slide-toggle';
import { MatTreeModule } from '@angular/material/tree';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import
{
  AvatarModule,
  BreadCrumbModule,
  LoadingModule
} from '@tei/material';
import { TreeListMenuActionsService } from '../../../@services/tree-list-menu-actions.service';
import { WebNavigationService } from '../../../@services/web-navigation.service';
import { ManagePageDialogModule } from './manage-page-dialog/manage-page-dialog.module';
import { WebNavigationRoutingModule } from './web-navigation-routing.module';
import { WebNavigationComponent } from './web-navigation.component';

const BASE_MODULES = [CommonModule, ReactiveFormsModule, WebNavigationRoutingModule];
const MATERIAL_MODULES = [MatCardModule, MatButtonToggleModule, MatTreeModule];
const TEI_MATERIAL_MODULES = [LoadingModule, BreadCrumbModule, MatLegacyFormFieldModule, AvatarModule,
  MatLegacyInputModule,
  MatLegacyOptionModule,
  MatLegacySelectModule,
  MatLegacySlideToggleModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];
const PAGE_MODULES = [ManagePageDialogModule];
const COMPONENTS = [WebNavigationComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...PAGE_MODULES
  ],
  providers: [{ provide: 'IWebNavigationService', useClass: WebNavigationService },
  { provide: 'DatabaseTemp', useClass: TreeListMenuActionsService },
  { provide: 'Database', useClass: TreeListMenuActionsService }]
})
export class WebNavigationModule { }
