import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService } from '@tei/core';
import { BreadCrumb, ImageUploaderComponent, ImageUploaderModel, SnackBarService } from '@tei/material';
import { Subscription } from 'rxjs';
import
{
  ApplicableBackgroundType, GetHomePageSettingRequest, GetHomePageSettingResponse,
  HomePageBackgroundType, Page,
  WebHomePageForm
} from '../../../@intermediate/models/get-web-home-page.model';
import { PostHomePageSettingResponse } from '../../../@intermediate/models/post-web-home-page.model';
import { IWebHomePageService } from '../../../@intermediate/web-home-page.interface';

@Component({
  selector: 'tei-web-home-page',
  templateUrl: './web-home-page.component.html',
  styleUrls: ['./web-home-page.component.scss'],
})
export class WebHomePageComponent implements OnInit, OnDestroy
{
  subscription = new Subscription();
  isLoading: boolean;
  BackgroundTypes: ApplicableBackgroundType[] = [];
  HomePageBackgroundType = HomePageBackgroundType;
  Pages: Page[];
  enableCallToActionText: boolean;
  isLogoRemoved: boolean = false;
  logoObjectFit: string = 'contain';
  backgroundObjectFit: string = 'cover';

  form: FormGroup<WebHomePageForm> = new FormGroup<WebHomePageForm>({
    LogoUrl: new FormControl(null),
    Logo: new FormControl(null),
    BackgroundType: new FormControl(HomePageBackgroundType.Image, Validators.required),
    BackgroundUrl: new FormControl(null),
    BackgroundImageFile: new FormControl(null),
    VideoURL: new FormControl(null),
    CallToActionText: new FormControl(null),
    EnableCallToAction: new FormControl(false),
    DisplayUpcomingEvents: new FormControl(false),
    CallActionPage: new FormControl(null),
  });

  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('MARKETING_CMS_ADMIN.WEB_HOME_PAGE.MARKETING'),
      relativeUrl: ''
    },
    {
      title: this.translate.instant('MARKETING_CMS_ADMIN.WEB_HOME_PAGE.CONTENT_MANAGEMENT'),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant('MARKETING_CMS_ADMIN.WEB_HOME_PAGE.WEB_HOME_PAGE'),
      relativeUrl: ''
    }
  ];

  constructor(
    private translate: TranslateService,
    public manageLoadingService: ManageLoadingService,
    private snackBarService: SnackBarService,
    @Inject('IWebHomePageService') private service: IWebHomePageService,
    private dialog: MatDialog,
  ) { }

  ngOnInit(): void
  {
    this.getWebHomePage();
    this.onBackgroundTypeChange();
    this.onEnableCallToAction();
  }

  onBackgroundTypeChange(): void
  {
    this.form.get('BackgroundType').valueChanges.subscribe((type: HomePageBackgroundType) =>
    {
      const BackgroundUrl = this.form.get('BackgroundUrl');
      const VideoURL = this.form.get('VideoURL');
      if (type === HomePageBackgroundType.Image)
      {
        BackgroundUrl.setValidators([Validators.required]);
        VideoURL.clearValidators();
      } else
      {
        VideoURL.setValidators([Validators.required]);
        BackgroundUrl.clearValidators();
      }
      BackgroundUrl.updateValueAndValidity();
      VideoURL.updateValueAndValidity();
    });
  }

  onEnableCallToAction(): void
  {
    this.form.get('EnableCallToAction').valueChanges.subscribe((isEnable: boolean) =>
    {
      this.enableCallToActionText = !isEnable;
      const CallToActionText = this.form.get('CallToActionText');
      const CallActionPage = this.form.get('CallActionPage');
      if (isEnable)
      {
        CallToActionText.setValidators([Validators.required]);
        CallActionPage.setValidators([Validators.required]);
      } else
      {
        CallToActionText.clearValidators();
        CallActionPage.clearValidators();
      }
      CallToActionText.updateValueAndValidity();
      CallActionPage.updateValueAndValidity();
    });
  }

  private getWebHomePage(): void
  {
    this.isLoading = true;
    const request = new GetHomePageSettingRequest();
    this.subscription.add(this.service.Get(request).subscribe((response: GetHomePageSettingResponse) =>
    {
      if (response)
      {
        this.isLoading = false;
        this.form.setValue({
          LogoUrl: response.LogoUrl,
          Logo: null,
          BackgroundType: response.BackgroundType,
          BackgroundUrl: response.BackgroundType === HomePageBackgroundType.Image ? response.BackgroundUrl : null,
          VideoURL: response.BackgroundType === HomePageBackgroundType.Video ? response.BackgroundUrl : null,
          BackgroundImageFile: null,
          EnableCallToAction: response.EnableCallToAction,
          DisplayUpcomingEvents: response.DisplayUpcomingEvents,
          CallToActionText: response.CallToActionText ?? '',
          CallActionPage: response.CallToActionPageUID
        });
        this.BackgroundTypes = response.BackgroundTypes;
        this.Pages = response.Pages;
      }
    }));
  }

  onChangeUpload(): void
  {
    const imageFormats = '.png';
    const imagePermittedFileTypes = ['PNG'];
    const data: ImageUploaderModel = {
      round: false,
      maintainAspectRatio: false,
      aspectRatio: 4 / 2.5,
      acceptFormat: imageFormats,
      permittedFileTypes: imagePermittedFileTypes,
      maxSize: 150,
      hideRemoveButton: true,
      origImage: this.formControls.Logo.value,
      imageServerUrl: this.formControls.LogoUrl.value,
    };
    const imageUploaderDialog = this.dialog.open(ImageUploaderComponent, {
      width: '400px',
      panelClass: ['o-dialog-md-fullscreen'],
      data,
    });
    imageUploaderDialog.afterClosed().subscribe((result) =>
    {
      if (result)
      {
        this.formControls.Logo.setValue(result?.origImage);
        this.formControls.LogoUrl.setValue(result?.croppedImage);
        this.logoObjectFit = 'contain';
        this.isLogoRemoved = false;
        if (!result?.croppedImage)
        {
          this.formControls.Logo.setValue(null);
          this.formControls.LogoUrl.setValue(null);
          this.isLogoRemoved = true;
        }
      }
    });
  }

  onBackgroundChangeUpload(): void
  {
    const imageFormats = '.jpg,.jpeg,.jpe,.jif,.jfif,.jfi,.png,.gif,.bmp,.dib,.tiff,.tif,.heif,.heic';
    const imagePermittedFileTypes = ['JPG', 'JPEG', 'JPE', 'JFIF', 'PNG', 'GIF', 'BMP', 'TIFF', 'HEIC'];
    const data: ImageUploaderModel = {
      round: false,
      maintainAspectRatio: false,
      aspectRatio: 4 / 2.5,
      acceptFormat: imageFormats,
      permittedFileTypes: imagePermittedFileTypes,
      maxSize: 500,
      hideRemoveButton: true,
      origImage: this.formControls.BackgroundImageFile.value,
      imageServerUrl: this.formControls.BackgroundUrl.value,
    };
    const imageUploaderDialog = this.dialog.open(ImageUploaderComponent, {
      width: '400px',
      panelClass: ['o-dialog-md-fullscreen'],
      data,
    });
    imageUploaderDialog.afterClosed().subscribe((result) =>
    {
      if (result)
      {
        this.formControls.BackgroundImageFile.setValue(result?.origImage);
        this.formControls.BackgroundUrl.setValue(result?.croppedImage);
        this.backgroundObjectFit = 'cover';
        if (!result?.croppedImage)
        {
          this.formControls.BackgroundImageFile.setValue(null);
          this.formControls.BackgroundUrl.setValue(null);
        }
      }
    });
  }

  onSave(): void
  {
    const formData = new FormData();
    formData.append('Logo', this.formControls.Logo.value);
    formData.append('LogoRemoved', this.isLogoRemoved.toString());
    formData.append('BackgroundType', this.formControls.BackgroundType.value.toString());
    if (this.formControls.BackgroundImageFile.value)
      formData.append('BackgroundImage', this.formControls.BackgroundImageFile.value);
    if (this.formControls.VideoURL.value)
      formData.append('BackgroundVideoUrl', this.formControls.VideoURL.value);

    formData.append('EnableCallToAction', this.formControls.EnableCallToAction.value.toString());
    if (this.formControls.CallToActionText.value)
      formData.append('CallToActionText', this.formControls.CallToActionText.value ?? '');
    if (this.formControls.CallActionPage.value)
      formData.append('CallToActionPageUID', this.formControls.CallActionPage.value);

    formData.append('DisplayUpcomingEvents', this.formControls.DisplayUpcomingEvents.value.toString());

    this.service.Post(formData).subscribe({
      next: (response: PostHomePageSettingResponse) =>
      {
        if (response)
        {
          this.snackBarService.showSuccess(this.translate.instant('MARKETING_CMS_ADMIN.BANNERS.SUCCESS_MESSAGE'), '', null, 'left');
          this.manageLoadingService.finishLoading();
        }
      }
    });
  }

  get formControls()
  {
    return this.form.controls;
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
