import { FormControl } from "@angular/forms";
import { NavigationItemIconModel } from "@tei/material";

export class GetPageRequest
{
  PageUID: string;
}

export interface GetPageResponse
{
  IconUrl: string;
  PageUID: string;
  PageName: string;
  PageTitle: string;
  PageIconType?: PageIconType;
  Icon: string;
  PageType: PageType;
  Module?: ModulePageType;
  Content?: ContentPageType;
  ExternalURL?: ExternalURLPageType;
  PageVisibilityRoles: string[];
  VisibleToAnonymous: boolean;
  DevicePageResponsiveSettings: DevicePageResponsiveSetting[];
  ApplicableModules: ApplicableModule[];
  ApplicableRoles: ApplicableRole[];
  ApplicableParentPages: ApplicableParentPage[];
  CMSInitialized: boolean;
}

export interface ApplicableModule
{
  ApplicationHierarchyUID: string;
  ApplicationHierarchyName: string;
}

export interface ApplicableRole
{
  DesignationName: string;
  Roles: Role[];
}

export interface Role
{
  RoleUID: string;
  RoleName: string;
}

export interface ApplicableParentPage
{
  PageUID: string;
  PageName: string;
}

export interface DevicePageResponsiveSetting
{
  DeviceTypeID: number;
  DeviceType: string;
  ParentPageUID?: string;
  DisplayPageInMenu: boolean;
  PageResponsiveSettingUID: string;
}

export interface ModulePageType
{
  ModuleApplicationHierarchyUID: string;
}

export interface ContentPageType
{
  ContentPageExternalIdentifier: string;
}

export interface ExternalURLPageType
{
  ExternalURL: string;
  OpenExternalURLInNewTab: boolean;
}

export enum PageType
{
  None = 0,
  Module = 1,
  Content = 2,
  ExternalUrl = 3,
}

export enum DeviceType
{
  Desktop = 1,
  Tablet = 2,
  Mobile = 3,
}

export enum PageIconType
{
  MatIcon = 1,
  File = 2,
}

export interface ManagePageForm
{
  PageUID: FormControl<string | null>;
  PageName: FormControl<string | null>;
  PageTitle: FormControl<string | null>;
  // IconType: FormControl<number | null>;
  Icon: FormControl<string | NavigationItemIconModel>;
  IconFile: FormControl<File>;
  IconUrl: FormControl<string | null>;
  PageType: FormControl<number | null>;
  Module: FormControl;
  ContentPageExternalIdentifier: FormControl;
  ExternalURL: FormControl<string | null>;
  OpenExternalURLInNewTab: FormControl<boolean | null>;
  PageVisibilityRoles: FormControl;
  VisibleToAnonymous: FormControl<boolean | null>;
  ApplicableModules: FormControl;
  ApplicableParentPage: FormControl;
  DisplayMenu: FormControl;
}

export class GetContentPagesRequest
{
}

export class GetContentPagesResponse
{
  ContentPages: ContentPages[];
}

export interface ContentPages
{
  PageName: string;
  ExternalIdentifier: string;
  PageType: string;
}
