import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { GetHomePageSettingRequest, GetHomePageSettingResponse } from '../@intermediate/models/get-web-home-page.model';
import { IWebHomePageService } from '../@intermediate/web-home-page.interface';
import { PostHomePageSettingResponse } from '../@intermediate/models/post-web-home-page.model';
@Injectable({
  providedIn: 'root'
})
export class WebHomePageService implements IWebHomePageService
{
  private readonly serviceUrl: string;
  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Marketing/CMS/Admin/HomePageSetting';
  }

  Get(request: GetHomePageSettingRequest): Observable<GetHomePageSettingResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.get<GetHomePageSettingResponse>(apiUrl, { params: request as any });
  }

  Post(request: FormData): Observable<PostHomePageSettingResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.post<PostHomePageSettingResponse>(apiUrl, request);
  }
}
