import { Location } from '@angular/common';
import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, SnackBarService, TeiChipsModel } from '@tei/material';
import moment from 'moment';
import { Subscription } from 'rxjs';
import { IBannerService } from '../../../../@intermediate/banner.interface';
import
{
  ApplicableDisplayType,
  ApplicableTriggerType, BannerDisplayType,
  BannerTriggerType
} from '../../../../@intermediate/models/get-banner.model';
import { BannerForm, Page, PostBannerRequest } from '../../../../@intermediate/models/post-banner.model';

@Component({
  selector: 'tei-cms-admin-new-banner',
  templateUrl: './new-banner.component.html',
  styleUrls: ['./new-banner.component.scss']
})
export class NewBannerComponent implements OnInit, OnDestroy
{
  selectedPage: Page[] = [];
  applicablePage: Page[];
  teiChipData: TeiChipsModel = new TeiChipsModel();
  applicableTriggerTypes: ApplicableTriggerType[];
  selectedTriggerType: BannerTriggerType;
  applicableDisplayTypes: ApplicableDisplayType[];
  private bannerId: string;
  subscription: Subscription = new Subscription();
  isLoading: boolean = false;
  editMode: boolean = false;
  currentDate: Date = new Date();
  defaultStartTime: Date = new Date(1995, 11, 17, 0, 0);
  defaultEndTime: Date = new Date(1995, 11, 17, 23, 45);
  breadCrumbData: BreadCrumb[] = [];
  form: FormGroup = new FormGroup<BannerForm>({
    BannerUID: new FormControl<string>(''),
    BannerIdentifier: new FormControl<string>('', Validators.required),
    Title: new FormControl<string>(''),
    Message: new FormControl<string>('', Validators.required),
    TriggerType: new FormControl(BannerTriggerType.PageLoad, Validators.required),
    Pages: new FormControl([], Validators.required),
    StartDateTime: new FormControl(new Date(new Date().setHours(0, 0, 0, 0)), Validators.required),
    EndDateTime: new FormControl(new Date(new Date(+new Date() + 86400000).setHours(0, 0, 0, 0)), Validators.required),
    StartTime: new FormControl<Date>(new Date(1995, 11, 17, 0, 0)),
    EndTime: new FormControl<Date>(new Date(1995, 11, 18, 0, 0)),
    DisabledOnMobile: new FormControl<boolean>(false, []),
    DisabledOnTablet: new FormControl<boolean>(false, []),
    DisabledOnDesktop: new FormControl<boolean>(false, []),
    StopShowingAfterDismiss: new FormControl<boolean>(false, []),
    DisplayType: new FormControl<number>(BannerDisplayType.TopBar, []),
    Status: new FormControl<boolean>(false, []),
  });

  constructor(
    private translate: TranslateService,
    private activatedRoute: ActivatedRoute,
    @Inject('IBannerService') private bannerService: IBannerService,
    private snackBarService: SnackBarService,
    private location: Location
  )
  {
  }

  ngOnInit(): void
  {
    const params = this.activatedRoute.snapshot.params;
    this.bannerId = params['id'];
    if (this.bannerId)
    {
      this.editMode = true;
      this.getBanner(this.bannerId);
    } else
    {
      this.getBanner();
    }
    this.breadCrumbData = this.createBreadCrumb();
  }

  private createBreadCrumb()
  {
    return [{
      title: this.translate.instant('MARKETING_CMS_ADMIN.BANNERS.MARKETING'),
      relativeUrl: ''
    },
    {
      title: this.translate.instant('MARKETING_CMS_ADMIN.BANNERS.BANNERS'),
      relativeUrl: this.editMode ? '../../' : '../'
    },
    {
      title: this.translate.instant('MARKETING_CMS_ADMIN.BANNERS.MANAGE'),
      relativeUrl: ''
    }];
  }

  getBanner(id?: string): void
  {
    this.isLoading = true;
    this.subscription.add(this.bannerService.GetByBannerId(id).subscribe(
      (result) =>
      {
        this.isLoading = false;
        if (id)
        {
          this.defaultStartTime = new Date(2024, 11, 17, 0, 0);
          this.defaultEndTime = new Date(2024, 11, 17, 23, 45);
          this.form.setValue({
            BannerUID: result.BannerUID,
            BannerIdentifier: result.BannerIdentifier,
            Title: result.Title,
            Message: result.Message,
            TriggerType: result.TriggerType,
            Pages: result.Pages?.map(m => (m.PageUID)),
            StartDateTime: result.StartDateTime,
            EndDateTime: result.EndDateTime,
            StartTime: new Date(result.StartDateTime),
            EndTime: new Date(result.EndDateTime),
            DisabledOnMobile: result.DisabledOnMobile,
            DisabledOnTablet: result.DisabledOnTablet,
            DisabledOnDesktop: result.DisabledOnDesktop,
            StopShowingAfterDismiss: result.StopShowingAfterDismiss,
            DisplayType: result.DisplayType,
            Status: result.Status
          });
        }
        this.applicablePage = result.ApplicablePages;
        this.teiChipData.allChips = this.applicablePage?.map(m => ({ id: m.PageUID, name: m.PageName, active: m.PageType })).sort((a, b) => a.name.localeCompare(b.name));
        this.applicableTriggerTypes = result.ApplicableTriggerTypes;
        this.selectedTriggerType = BannerTriggerType.PageLoad;
        this.applicableDisplayTypes = result.ApplicableDisplayTypes;
      },
      (err) =>
      {
        this.isLoading = false;
      }));
  }

  compareTwoDates(): boolean
  {
    return new Date(this.form.controls['EndDateTime'].value) < new Date(this.form.controls['StartDateTime'].value);
  }

  onSave(): void
  {
    this.selectedPage = [];
    this.form.value.Pages.forEach((page: any) =>
    {
      this.selectedPage.push(this.applicablePage.find(value =>
      {
        return page == value.PageUID ? value : null;
      }));
    });
    this.isLoading = true;
    const request: PostBannerRequest = {
      BannerIdentifier: this.form.value.BannerIdentifier,
      DisabledOnDesktop: this.form.value.DisabledOnDesktop !== null ? this.form.value.DisabledOnDesktop : false,
      DisabledOnMobile: this.form.value.DisabledOnMobile !== null ? this.form.value.DisabledOnMobile : false,
      DisabledOnTablet: this.form.value.DisabledOnTablet !== null ? this.form.value.DisabledOnTablet : false,
      DisplayType: this.form.value.DisplayType !== null ? this.form.value.DisplayType : BannerDisplayType.TopBar,
      StartDateTime: moment(this.form.value.StartDateTime).format("YYYY-MM-DD[T]") + moment(this.form.value.StartTime).format('HH:mm:ss'),
      EndDateTime: moment(this.form.value.EndDateTime).format("YYYY-MM-DD[T]") + moment(this.form.value.EndTime).format('HH:mm:ss'),
      Message: this.form.value.Message,
      Pages: this.selectedPage,
      Status: this.form.value.Status !== null ? this.form.value.Status : false,
      StopShowingAfterDismiss: this.form.value.StopShowingAfterDismiss !== null ? this.form.value.StopShowingAfterDismiss : false,
      Title: this.form.value.Title,
      TriggerType: this.form.value.TriggerType
    };
    if (this.bannerId)
    {
      request.BannerUID = this.bannerId;
    }

    this.subscription.add(this.bannerService.Post(request).subscribe(
      () =>
      {
        this.isLoading = false;
        this.snackBarService.showSuccess(this.translate.instant('MARKETING_CMS_ADMIN.BANNERS.SUCCESS_MESSAGE'), '', null, 'left');
        if (!this.editMode)
        {
          this.location.back();
        }
      },
      (err) =>
      {
        this.isLoading = false;
      }));
  }

  getFormControlByName(name: string): FormControl
  {
    return this.form?.get(name) as FormControl;
  }

  ngOnDestroy()
  {
    this.subscription.unsubscribe();
  }
}
