import { DragDropModule } from '@angular/cdk/drag-drop';
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyPaginatorModule as MatPaginatorModule } from '@angular/material/legacy-paginator';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { TeiDateTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, FilterChipsModule, LoadingModule } from '@tei/material';
import { BannerSortOrderComponent } from './banner-sort-order.component';
import { BannerService } from '../../../../@services/banner.service';


const BASE_MODULES = [FormsModule, CommonModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatCardModule, MatButtonModule, MatPaginatorModule, LoadingModule, DragDropModule];
const TEI_MATERIAL_MODULES = [BreadCrumbModule, MatTableModule, FilterChipsModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule, TeiDateTranslateModule];
const COMPONENTS = [BannerSortOrderComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
  ],
  providers: [{ provide: 'IBannerService', useClass: BannerService }]
})
export class BannerSortOrderModule { }
