import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable, map } from 'rxjs';
import {
  Banner,
  DeleteBannerRequest,
  GetBannersRequest,
  GetBannersResponse, ReorderBannersRequest, ReorderBannersResponse
} from '../@intermediate/models/get-banner.model';
import { IBannerService } from '../@intermediate/banner.interface';
import { PostBannerRequest, PostBannerResponse } from '../@intermediate/models/post-banner.model';

@Injectable({
  providedIn: 'root'
})

export class BannerService implements IBannerService
{
  private readonly serviceUrl: string;
  private readonly bannerServiceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Marketing/CMS/Admin/Banners';
    this.bannerServiceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Marketing/CMS/Admin/Banner';
  }

  public Get(request: GetBannersRequest): Observable<GetBannersResponse> {
    const apiUrl = this.serviceUrl;
    return this.http.get<GetBannersResponse>(apiUrl, { params: request as any })
      .pipe(map(response => response));
  }

  public GetByBannerId(request?: string): Observable<any> {
    const apiUrl = `${this.bannerServiceUrl}`;
    if(request){
      return this.http.get<Banner>(`${apiUrl}/${request}`).pipe(map(response => response));
    }
    return this.http.get<Banner>(apiUrl).pipe(map(response => response));
  }

  public Post(request: PostBannerRequest): Observable<PostBannerResponse> {
    const apiUrl = `${this.serviceUrl.slice(0, -1)}`;
    return this.http.post<PostBannerResponse>(apiUrl, request);
  }

  public DeleteBanner(request: DeleteBannerRequest): Observable<void> {
    const apiUrl = `${this.serviceUrl}/${request.BannerUID}`;
    return this.http.delete<void>(apiUrl)
      .pipe(map(response => response));
  }

  public GetAll(): Observable<ReorderBannersResponse> {
    const apiUrl = `${this.serviceUrl}/All`;
    return this.http.get<ReorderBannersResponse>(apiUrl)
      .pipe(map(response => response));
  }

  public ReorderBanners(request: ReorderBannersRequest): Observable<ReorderBannersResponse> {
    const apiUrl = `${this.serviceUrl}/Reorder`;
    return this.http.put<ReorderBannersResponse>(apiUrl, request)
  }
}
