import { FormControl } from '@angular/forms';

export class GetHomePageSettingRequest
{
}

export class GetHomePageSettingResponse
{
  LogoUrl: string;
  BackgroundType: HomePageBackgroundType;
  BackgroundUrl: string;
  VideoURL: string;
  EnableCallToAction: boolean;
  CallToActionText: string;
  CallToActionPageUID: string;
  DisplayUpcomingEvents: boolean;
  BackgroundTypes: ApplicableBackgroundType[];
  Pages: Page[];
}

export interface WebHomePageForm
{
  LogoUrl: FormControl<string>;
  Logo: FormControl<File>;
  BackgroundUrl: FormControl<string>;
  VideoURL: FormControl<string>;
  CallToActionText: FormControl<string>;
  CallActionPage: FormControl<string>;
  BackgroundImageFile?: FormControl<File>;
  BackgroundType?: FormControl<HomePageBackgroundType>;
  EnableCallToAction?: FormControl<boolean>;
  DisplayUpcomingEvents?: FormControl<boolean>;
}

export class ApplicableBackgroundType
{
  BackgroundTypeID: HomePageBackgroundType;
  BackgroundTypeDescription: string;
}

export class Page
{
  PageUID: string;
  PageTitle: string;
}

export enum HomePageBackgroundType
{
  Image = 1,
  Video = 2
}
