import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable, map } from 'rxjs';
import
{
  GetPageStyleSheetRequest,
  GetPageStyleSheetResponse
} from '../@intermediate/models/get-page-style-sheet.model';
import { PostPageStyleSheetRequest } from '../@intermediate/models/post-page-style-sheet-request.model';
import { IPageStyleSheetService } from '../@intermediate/page-style-sheet.interface';
import { GetHomePageSettingResponse } from '../@intermediate/models/get-web-home-page.model';
import { PostHomePageSettingResponse } from '../@intermediate/models/post-web-home-page.model';

@Injectable({
  providedIn: 'root'
})
export class PageStyleSheetService implements IPageStyleSheetService
{
  private readonly serviceUrl: string;

  constructor(
    @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    private http: HttpClient
  )
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Marketing/CMS/Admin/Page/PageStyleSheet';
  }

  public Get(request: GetPageStyleSheetRequest): Observable<GetPageStyleSheetResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.get<GetPageStyleSheetResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostPageStyleSheetRequest): Observable<void>
  {
    const apiUrl = this.serviceUrl;
    return this.http.post<void>(apiUrl, request);
  }
}
