import { FormControl } from '@angular/forms';

export class GetNavigationSettingRequest
{
}

export class GetNavigationSettingResponse
{
  EnableCustomNavigation: boolean;
  DesktopNavigationType: number;
  DesktopNavigationTypes: AcceptableDesktopNavigationType[]
  HomePageUID: string;
  ApplicablePagesForHomePage: ApplicablePageForHomePage[];
  EnableCustomizeResponsiveBreakpoints: boolean;
  DesktopCustomizedWidth: number;
  TabletCustomizedWidth: number;
}

export class ApplicablePageForHomePage
{
  PageUID: string;
  PageName: string;
}

export class AcceptableDesktopNavigationType
{
  DesktopNavigationTypeID: string
  DesktopNavigationTypeDescription: string;
}

export enum DeviceType
{
  Desktop = 1,
  Tablet = 2,
  Mobile = 3
}

export interface WebNavigationForm
{
  EnableCustomNavigation: FormControl<boolean>;
  EnableCustomizeResponsiveBreakpoints: FormControl<boolean>;
  DesktopNavigationType: FormControl<number>;
  DesktopCustomizedWidth: FormControl<number>;
  TabletCustomizedWidth: FormControl<number>;
  HomePageUID: FormControl<string>;
  DeviceType?: FormControl<DeviceType>;
}
